<template>
    <div class="balance">
        <!--顶部我的余额-->
        <div class="detail_top"><van-nav-bar title="我的余额" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--金额提现-->
        <div class="bala">
            <div class="iniside_bala">
                <div class="inside_top">
                    <span>当前余额</span>
                    <router-link :to="{path:'/balance/detail'}">
                        <i class="iconfont pull-right">&#xe661;<span>余额说明</span></i>
                    </router-link>
                </div>
                <p>{{money}}<span>元</span></p>
                <button class="btn btn-default btn-lg btn-block" disabled>提现</button>
            </div>
        </div>
        <!--交易明细-->
        <p class="detail">交易明细</p>
        <div class="record">
            <img :src="imgUrl" alt="">
            <p>暂无明细记录</p>
        </div>
    </div>
</template>

<script>
    export default {
        name: "balance",
        data(){
            return{
                money:"0.00",
                imgUrl:"data:image/png;base64,"
            }
        },
        methods:{
            onClickLeft(){
                this.$router.push("/order1/profile")
            }
        }
    }
</script>

<style scoped>
    /*返回*/
    a{
        margin-left: 1rem;
        color: white;
        z-index: 500;
    }
    /*问号*/
    .iconfont{
        color: #3190e8;
        font-size: 1.7rem;
    }
    /*提现金额*/
    .bala{
        border-top: 1px solid #4a9dea;
        width: 100%;
        height: 17.5rem;
        background: #3190e8;
    }
    .iniside_bala{
        background: white;
        width: 96%;
        margin: 1rem 2%;
        border-radius: 0.3rem;
        padding: 1rem;
    }
    i>span{
        font-size: 1.3rem;
        font-weight: 200;
        vertical-align: 0.2rem;
        margin-left: 0.5rem;
    }
    .inside_top>span:nth-child(1){
        font-size: 1.3rem;
    }
    .iniside_bala>p{
        font-size: 4rem;
        margin-bottom: 0px;
    }
    .iniside_bala>p>span{
        font-size: 1.7rem;
    }
    .btn{
        margin-top: 1rem;
        font-size: 2rem;
    }
    .btn:disabled{
        background: #a6a6a6;
        color: white;
    }
    /*交易明细*/
    .detail{
        line-height: 5rem;
        padding-left: 1rem;
        font-size: 1.5rem;
        color: #999;
    }
    .record{
        text-align: center;
    }
    .record>img{
        width: 19rem;
        height: 12rem;
    }
    .record>p{
        margin-top: 2rem;
        font-size: 1.5rem;
        color: #666;
    }
</style>


