<template>
    <div class="search">
        <!--搜索-->
        <div class="detail_top"><van-nav-bar title="搜索" left-arrow @click-left="onClickLeft" fixed="true"></van-nav-bar></div>
        <!--搜索框-->
        <div class="sousuok">
            <!--输入框-->
            <input type="text" placeholder="请输入商家或美食名称" v-model="cate" @input="showCuo">
            <!--xxx-->
            <span class="glyphicon glyphicon-remove cuo" v-show="isShowCuo" @click="deleteInput"></span>
            <button class="pull-right" @click="seekCate">提交</button>
        </div>
        <!--商家-->
        <p class="shangjia" v-show="isText">{{textsl}}</p>
        <!--商家列表-->
        <div class="shangjia_list" v-for="(pro,index) in allShangjia" v-show="isSSJLx">
            <img :src="'//elm.cangdu.org/img/'+pro.image_path" alt="">
            <div class="shangjia_xiangq pull-right">
                <p>
                    {{pro.name}}
                    <img src="../../static/imgs/zhifu.png" alt="">
                </p>
                <p>月售 {{pro.recent_order_num}} 单</p>
                <p>{{pro.float_minimum_order_amount}} 元起送 / 距离{{pro.distance}}</p>
            </div>
            <div class="clearfix"></div>
        </div>
        <!--历史记录-->
        <div class="lishijilu" v-for="(pro,index) in historyAll" v-show="isSSJL">
            <p>{{pro}}</p>
            <span class="pull-right" @click="quYiGe(index)">x</span>
        </div>
        <p class="deleteHis" v-show="isSSJL" @click="qingkong">清空搜索记录</p>

        <!--抱歉-->
        <p class="baoqian" v-show="isBaoqian">很抱歉无搜索结果</p>
    </div>
</template>

<script>
    import Vue from "vue"
    export default {
        name: "Search",
        data(){
            return{
                //输入框内容
                cate:"",
                //xxx的隐藏或显示
                isShowCuo:false,
                //经纬度
                geo:"",
                //搜索的商家信息
                allShangjia:[],
                //商家或搜索历史
                textsl:"商家",
                isText:false,
                //是否隐藏或显示
                isBaoqian:false,
                //搜索记录
                isSSJL:false,
                //历史记录
                historyAll:[],
                //商家列表
                isSSJLx:false,
            }
        },
        //方法
        methods: {
            onClickLeft() {
                this.$router.back(-1);
            },
            //是否显示x号
            showCuo(){
                if (this.cate.length==0){
                    this.isShowCuo=false;
                } else{
                    this.isShowCuo=true;
                }
            },
            //是否清空
            deleteInput(){
                this.cate="";
                this.isShowCuo=false;
                this.isBaoqian=false;
                if (this.historyAll.length==0){
                    this.isText=false;
                } else{
                    this.isText=true;
                    this.textsl="搜索历史";
                    this.isSSJL=true;
                    this.isSSJLx=false;
                }
            },
            //搜索商家或美食
            seekCate(){
                if (this.cate.length!=0){
                    Vue.axios.get(`https://elm.cangdu.org/v4/restaurants?geohash=${this.geo}&keyword=${this.cate}`).then((result)=>{
                        this.allShangjia=result.data;
                        if (this.allShangjia.length==0){
                            this.isBaoqian=true;
                            this.isText=false;
                            this.isSSJL=false;
                            this.isSSJLx=true;
                        } else{
                            this.isBaoqian=false;
                            this.isText=true;
                            this.textsl="商家";
                            this.isSSJL=false;
                            this.isSSJLx=true;
                        }
                    }).catch((error)=>{
                        console.log(error);
                    })
                }
                if (this.historyAll.indexOf(this.cate)==-1){
                    this.historyAll.push(this.cate);
                    localStorage.setItem('historySjMs',JSON.stringify(this.historyAll));
                };
            },
            qingkong(){
                localStorage.removeItem("historySjMs");
                this.isSSJL=false;
                this.isText=false;
                this.isSSJLx=false;
            },
            quYiGe(index){
                this.historyAll.splice(index,1);
                if (this.historyAll.length==0){
                    this.isSSJL=false;
                    this.isText=false;
                }
                localStorage.setItem('historySjMs',JSON.stringify(this.historyAll));
            }
        },
        computed:{
            localXX(){
                return JSON.parse(localStorage.getItem('local'));
            }
        },
        created(){
            if(this.$route.path=="/order1/search"){
                this.$store.commit("setActive",1);
            };
            Vue.axios.get(`https://elm.cangdu.org/v2/pois/${this.localXX[0].geohash}`).then((result)=>{
                this.geo=result.data.geohash;
            }).catch((error)=>{
                console.log(error);
            });
            this.historyAll=JSON.parse(localStorage.getItem('historySjMs'))||[];
            if (this.historyAll.length!=0){
                this.textsl="搜索历史";
                this.isText=true;
                this.isSSJL=true;
                this.isSSJLx=false;
            }
        }
    }
</script>

<style scoped>
    .search{
        padding-bottom: 7rem;
    }
    /*输入框*/
    .sousuok{
        width: 100%;
        background: white;
        padding: 5rem 1rem 1rem;
        position: relative;
    }
    /*输入框样式*/
    .sousuok input{
        width: 75%;
        height: 3.5rem;
        border: 0.1rem solid #ededed;
        background: #f2f2f2;
        border-radius: 0.5rem;
        padding-left: 1rem;
        font-size: 1.5rem;
        font-weight: bold;
    }
    /*搜索按钮*/
    .sousuok button{
        width: 23%;
        height: 3.5rem;
        border: none;
        border-radius: 0.5rem;
        background: #3190e8;
        color: white;
        font-size: 1.5rem;
        font-weight: bold;
    }
    /*xxx*/
    .cuo{
        position: absolute;
        top: 6.1rem;
        right: 10.5rem;
        font-size: 1.3rem;
        color: darkblue;
        opacity: 0.7;
    }
    /*商家*/
    .shangjia{
        font-size: 1.3rem;
        font-weight: bold;
        line-height: 4rem;
        padding-left: 1rem;
    }
    /*商家列表-图片*/
    .shangjia_list{
        padding: 1.5rem 1rem;
        background: white;
        border-bottom: 0.1rem solid #d9d9d9;
    }
    .shangjia_list img{
        width: 10%;
    }
    /*右侧商家信息*/
    .shangjia_xiangq{
        width: 87%;
        display: inline-block;
        border-bottom: 0.1rem solid #d9d9d9;
        font-size: 1.2rem;
        line-height: 2rem;
    }
    .shangjia_xiangq p img{
        width: 2rem;
    }
    .shangjia_xiangq p:nth-child(3){
        padding-bottom: 0.7rem;
    }
    .baoqian{
        font-size: 1.5rem;
        text-align: center;
        line-height: 5rem;
        background: white;
        border-top: 0.1rem solid #d9d9d9;
    }
    /*历史记录*/
    .lishijilu{
        width: 100%;
        font-size: 1.5rem;
        line-height: 3rem;
        background: white;
        padding: 0.5rem 1rem;
        border-bottom: 0.1rem solid #d9d9d9;
    }
    .lishijilu p{
        display: inline-block;
    }
    .deleteHis{
        width: 100%;
        font-size: 1.5rem;
        font-weight: bold;
        line-height: 4rem;
        text-align: center;
        background: white;
        color: #3190e9;
    }
</style>



