<template>
    <div class="addDetail">
        <!--搜索地址-->
        <div class="detail_top"><van-nav-bar title="搜索地址" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--搜索-->
        <div class="search">
            <input type="text" placeholder="请输入小区/写字楼/学校等" v-model="GetAddress">
            <button class="pull-right" @click="searchAdd">确认</button>
        </div>
        <!--建议-->
        <div class="ont_address">
            <p>为了满足商家的送餐要求，建议您从列表中选择地址</p>
        </div>
        <!--搜索出的地址-->
        <div class="address_all" v-for="(pro,index) in addressAll" v-show="no_show" @click="ToAddress(index)">
            <p>{{pro.name}}</p>
            <p>{{pro.address}}</p>
        </div>
        <div class="bottom_text" v-show="JSON.stringify(addressAll)=='{}'||addressAll.message=='参数错误'">
            <p>找不到地址？</p>
            <p>请尝试输入小区、写字楼或学校名</p>
            <p>详细地址（如门牌号）可稍后输入哦。</p>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'//引入vue
    export default {
        name: "AddDetail",
        data(){
          return{
              GetAddress:"",
              //存储所有城市地址
              addressAll:{},
          }
        },
        //计算属性
        computed:{
            city_id(){
                return this.$store.state.p.city_id;
            },
            no_show(){
                if (this.addressAll.message==="参数错误") {
                    return false;
                }else {
                    return true;
                }
            }
        },
        methods: {
            onClickLeft() {
                this.$router.push({path: "/profile/info/address/add"});
            },
            searchAdd(){
                Vue.axios.get("https://elm.cangdu.org/v1/pois",{params:{city_id:this.city_id,keyword:this.GetAddress}}).then((result)=>{
                    this.addressAll=result.data;
                    console.log(result.data);
                }).catch((error)=>{
                    console.log(error);
                })
            },
            ToAddress(index){
                this.$router.push({name:"add",params:this.addressAll[index]})
            }
        },
        created(){
            this.GetAddress=this.$route.params.add;
        }
    }
</script>

<style scoped>
.search{
    width: 100%;
    height: 7rem;
    padding: 1.5rem 2rem;
    background: white;
}
.search input{
    width: 75%;
    height: 4rem;
    border: 0.1rem solid #d9d9d9;
    background: #f2f2f2;
    border-radius: 0.5rem;
    font-size: 1.5rem;
    padding-left: 1rem;
}
.search button{
    width: 23%;
    height: 4rem;
    border-radius: 0.5rem;
    border: none;
    background: #3199e8;
    font-size: 1.7rem;
    color: white;
}
.ont_address{
    width: 100%;
    height: 3.5rem;
    line-height: 3.5rem;
    background: #fff6e4;
    font-size: 1.4rem;
    color: #ff883f;
    text-align: center;
}
.bottom_text{
    text-align: center;
    font-size: 1.5rem;
    color: #969696;
    margin-top: 15rem;
    line-height: 3rem;
}
.address_all{
    width: 100%;
    border-bottom: 0.1rem solid #d9d9d9;
}
.address_all p:nth-child(1){
    margin-top: 1rem;
}
.address_all p:nth-child(2){
    margin: 1rem 0rem;
    line-height: 3rem;
}
.address_all p{
    padding: 0rem 2rem;
    font-size: 1.5rem;
    color: #999;
}
</style>


