<template>
    <div class="payment">
        <!--顶部-->
        <div class="detail_top"><van-nav-bar title="在线支付" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--倒计时-->
        <div class="count">
            <p>剩余支付时间</p>
            <van-count-down :time="time" />
        </div>
        <!--选择支付方式-->
        <p class="way">选择支付方式</p>
        <div class="manner">
            <div class="manner_one">
                <i class="iconfont">&#xe65f;</i>
                <span>支付宝</span>
                <van-icon name="checked" class="pull-right" v-show="!showGreen" @click="showGreen=!showGreen"/>
                <van-icon name="checked" class="pull-right" color="#4cd964" v-show="showGreen"/>
            </div>
            <div class="manner_one">
                <svg viewBox="0 0 72 72" id="weixin"><g fill="none" fill-rule="evenodd"><path d="M36 72c19.882 0 36-16.118 36-36S55.882 0 36 0 0 16.118 0 36s16.118 36 36 36z" fill="#45C144"></path><path d="M29 40l-4-8c-.262-.292-.288-.398 0-1 .594-.228.743-.174 1 0l4 4c1.5 1.098 1.5 1.098 3 0l21-10c-3.668-4.964-10.11-8-17-8-12.33 0-22 8.12-22 18 0 5.6 2.91 10.518 7 14 1 .8 1.068.537 1 1 .068.185-.954 4.042-1 4-.027.316-.097.495 0 1-.097.075.225.4 1 0-.22.4 5-3 5-3 1.4.6 5.8 1 9 1 11.533 0 21.2-7.85 21-18 .2-2.899-.692-5.756-2-8L31 41c-1.5.5-2-1-2-1z" fill="#FFF"></path></g></svg>
                <span>微信</span>
                <van-icon name="checked" class="pull-right" v-show="showGreen" @click="showGreen=!showGreen"/>
                <van-icon name="checked" class="pull-right" color="#4cd964" v-show="!showGreen"/>
            </div>
        </div>
        <!--确认支付-->
        <div class="Ysuccess">
            <button @click="ToSuccess">确认支付</button>
        </div>

        <!--弹窗-->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show">确认</button>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "Payment",
        data(){
            return{
                time: 901000,
                //是否选中
                showGreen:true,
                //弹窗
                show:false,
                text:"",
            }
        },
        methods: {
            onClickLeft() {
                this.$router.push({path:"/Vipcard"});
            },
            ToSuccess(){
                this.show=true;
                this.text="当前环境无法支付,请打开官方APP进行付款";
            }
        },
        mounted(){
            this.show=true;
            this.text="暂不开放支付功能";
        }
    }
</script>

<style scoped>
.count{
    height: 15rem;
    background: white;
}
.van-count-down{
    text-align: center;
    font-size: 3.5rem;
    line-height: 5rem;
    letter-spacing: 0.3rem;
}
.count p{
    font-size: 1.5rem;
    text-align: center;
    padding-top: 4rem;
}
.way{
    font-size: 1.7rem;
    padding: 1.2rem 2rem;
}
.manner_one{
    width: 100%;
    height: 8rem;
    background: white;
    padding: 0rem 2rem;
    border-bottom: 1px solid #f9f9f9;
}
.manner_one i:nth-child(1){
    font-size: 5rem;
    line-height: 8rem;
    color: #00a0ec;
}
.manner_one span{
    font-size: 1.5rem;
    vertical-align: 1.5rem;
}
.van-icon:nth-child(3),.van-icon:nth-child(4){
    font-size: 2.5rem;
    top: 2.75rem;
    color: #ccc;
}
#weixin{
    width: 5rem;
    margin-top: 1.5rem;
}
.Ysuccess{
    width: 100%;
    padding: 1.5rem 2rem;
    text-align: center;
}
.Ysuccess button{
    width: 100%;
    height: 4rem;
    font-size: 1.5rem;
    border: none;
    background: #4cd964;
    border-radius: 0.5rem;
    color: white;
}
/*弹窗*/
.tanchuang span{
    padding: 0rem 1.5rem;
    line-height: 2rem;
}
</style>


