<template>
    <div class="infoaddress">
        <!--顶部编辑地址-->
        <div class="detail_top">
            <van-nav-bar title="编辑地址" left-arrow @click-left="onClickLeft" :right-text="changeBZ?'编辑':'完成'" @click-right="onClickRight"></van-nav-bar>
        </div>
        <!--显示地址-->
        <ul>
            <li v-for="(pro,index) in songAdd">
                <div>
                    <p>{{pro.address_detail}}</p>
                    <p>{{pro.phone}}</p>
                </div>
                <div class="pull-right" @click="deleteAdd(index)" v-show="!changeBZ">x</div>
            </li>
        </ul>
        <!--新增地址-->
        <van-cell title="新增地址" is-link :to="{path:'/profile/info/address/add'}"/>
    </div>
</template>

<script>
    import Vue from 'vue'//引入vue
    export default {
        name: "InfoAddress",
        data(){
            return{
                songAdd:[],
                //编辑和完成转换
                changeBZ:true,
            }
        },
        //事件
        methods: {
            onClickLeft() {
                this.$router.push({path: "/profile/info"});
            },
            //编辑和完成的改变
            onClickRight(){
                this.changeBZ=!this.changeBZ
            },
            //删除地址
            deleteAdd(index){
                console.log(this.songAdd[index]);
                Vue.axios.delete(`https://elm.cangdu.org/v1/users/${this.username.user_id}/addresses/${this.songAdd[index].id}`).then((result)=>{
                    if (result.data.success=="删除地址成功") {
                        Vue.axios.get(`https://elm.cangdu.org/v1/users/${this.username.user_id}/addresses`).then((result)=>{
                            this.songAdd=result.data||[];
                            console.log(this.songAdd);
                        }).catch((error)=>{
                            console.log(error);
                        })
                    }
                }).catch((error)=>{
                    console.log(error);
                })
            }
        },
        //计算属性
        computed: {
            username() {
                return this.$store.state.p.ZYMpublic
            },
        },
        created(){
            Vue.axios.get(`https://elm.cangdu.org/v1/users/${this.username.user_id}/addresses`).then((result)=>{
                this.songAdd=result.data||[];
                console.log(this.songAdd);
            }).catch((error)=>{
                console.log(error);
            })
        }
    }
</script>

<style scoped>
/*顶部编辑地址*/
.detail_top{
    margin-bottom: 1rem;
}
.van-cell{
    width: 100%;
    height: 5rem;
    padding: 0rem 2rem;
}
.van-cell div{
    width: 100%;
    height: 100%;
}
.van-cell div span{
    line-height: 5rem;
    font-size: 1.5rem;
}
.van-cell i{
    line-height: 5rem;
    font-size: 1.5rem;
}
.van-nav-bar div span{
    color: white;
}
.van-nav-bar div:nth-child(3) span{
    font-size: 1.5rem;
    background: none;
    margin-top: -0.3rem;
}
/*ul标签显示地址*/
ul{
    width: 100%;
    margin-bottom: 1rem;
}
ul li{
    width: 100%;
    padding: 1rem 2rem;
    border: 0.1rem solid #d9d9d9;
    background: white;
}
ul li:nth-child(1){
    background: #fff8c3;
}
li p{
    width: 100%;
    height: 2rem;
    font-size: 1.5rem;
    line-height: 2rem;
}
li div:nth-child(1){
    display: inline-block;
    width: 50%;
}
li div:nth-child(2){
    font-size: 1.5rem;
    color: #999;
    line-height: 4rem;
}
</style>


