<template>
    <div class="orderDetail" v-if="allOrders.status_bar">
        <!--顶部订单-->
        <div class="detail_top"><van-nav-bar title="订单详情" left-arrow @click-left="onClickLeft" fixed="true"></van-nav-bar></div>
        <!--商家头像-->
        <div class="userImg" v-if="isLoading">
            <img :src="'//elm.cangdu.org/img/'+allOrders.restaurant_image_url" alt="">
            <p>{{allOrders.status_bar.title}}</p>
            <button>再来一单</button>
        </div>
        <!--购买信息-->
        <div class="purchase" v-if="isLoading">
            <div class="pur_one">
                <img :src="'//elm.cangdu.org/img/'+allOrders.restaurant_image_url" alt="">
                <span>{{allOrders.restaurant_name}}</span>
                <van-icon name="arrow" class="pull-right"/>
            </div>
            <div class="pur_two">
                <span>{{allOrders.basket.group[0][0].name}}</span>
                <span class="pull-right"><span class="shuliang">x{{allOrders.total_quantity}}</span>￥{{allOrders.basket.group[0][0].price}}</span>
            </div>
            <div class="pur_three">
                <span>配送费</span>
                <span class="pull-right">{{allOrders.basket.deliver_fee.price}}</span>
            </div>
            <div class="pur_four">
                <span class="pull-right">实付{{Math.floor(allOrders.total_amount)}}.{{allOrders.total_amount-Math.floor(allOrders.total_amount)}}0</span>
            </div>
            <div class="clearfix"></div>
        </div>
        <!--配送信息-->
        <div class="distribution" v-if="isLoading">
            <div class="dis_one">配送信息</div>
            <div class="dis_two">送达时间：{{allOrderDetail.deliver_time}}</div>
            <div class="dis_three">
                送货地址：
                <div class="pull-right">
                    <p>{{allOrderDetail.consignee}}</p>
                    <p>{{allOrderDetail.phone}}</p>
                </div>
            </div>
            <div class="clearfix"></div>
            <div class="dis_four">配送方式：蜂鸟配送</div>
        </div>
        <!--订单信息-->
        <div class="distribution ddxinxi" v-if="isLoading">
            <div class="dis_one">订单信息</div>
            <div class="dis_p">
                <p>订单号:{{allOrders.unique_id}}</p>
                <p>支付方式:{{allOrderDetail.pay_method}}</p>
                <p>下单时间:{{allOrders.formatted_created_at}}</p>
            </div>
        </div>



        <!--动画效果-->
        <div class="elmloading" v-if="!isLoading">
            <div class="imgbox">
                <div class="imgbackground">
                </div>
            </div>
            <div class="loadfoot">
                <span class="footspan"></span>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from "vue"
    export default {
        name: "OrderDetail",
        data(){
            return{
                allOrders:[],
                allOrderDetail:[],
                isLoading:true,
            }
        },
        methods: {
            onClickLeft() {
                this.$router.push({path:"/order1/order"});
            },
        },
        created(){
            this.allOrders=this.$store.state.p.dingdan;
            Vue.axios.get(`https://elm.cangdu.org/bos/v1/users/${this.$store.state.p.ZYMpublic.user_id}/orders/${this.allOrders.unique_id}/snapshot`).then((result)=>{
                this.allOrderDetail=result.data;
                if (this.allOrderDetail.message=="获取订单信息失败"){
                    this.isLoading=false;
                }else{
                    this.isLoading=true;
                }
            }).catch((error)=>{
                console.log(error);
            })
        }
    }
</script>

<style scoped>
    /*商家头像外框*/
    .userImg{
        width: 100%;
        height: 20rem;
        text-align: center;
        background: white;
        margin-top: 4rem;
    }
    /*商家头像img*/
    .userImg img{
        width: 7rem;
        border-radius: 50%;
        border: 0.1rem solid #3793e9;
        margin-top: 2.5rem;
    }
    /*支付状态*/
    .userImg p{
        font-size: 2rem;
        font-weight: bold;
        line-height: 4rem;
    }
    /*再来一单*/
    .userImg button{
        width: 8rem;
        height: 2.8rem;
        font-size: 1.5rem;
        border-radius: 0.3rem;
        background: none;
        border: 0.1rem solid #3190e8;
        color: #3190e8;
        margin-top: 1.5rem;
    }
    /*购买信息*/
    .purchase{
        width: 100%;
        background: white;
        margin-top: 1rem;
    }
    .purchase div{
        padding: 1rem 2rem;
        border-bottom: 0.1rem solid #f1f1f1;
    }
    /*购买信息one*/
    .pur_one img{
        width: 3rem;
    }
    .pur_one span{
        font-size: 2rem;
        line-height: 3rem;
        padding-left: 1rem;
        font-weight: bold;
        vertical-align: top;
    }
    .pur_one i{
        font-size: 1.5rem;
        line-height: 3rem;
    }
    /*商品单价和数量*/
    .pur_two,.pur_three span{
        font-size: 1.5rem;
        line-height: 2rem;
    }
    .pur_two>span>span{
        color: #d9d9d9;
        margin-right: 2rem;
    }
    .pur_four{
        border-bottom: none!important;
    }
    .pur_four span{
        font-size: 1.8rem;
        line-height: 2rem;
        font-weight: bold;
        color: #fb6b23;
    }
    /*配送方式*/
    .distribution{
        margin-top: 1rem;
        width: 100%;
        background: white;
    }
    .distribution div{
        padding: 0rem 2rem;
    }
    .dis_one{
        font-size: 2rem;
        line-height: 4rem;
        border-bottom: 0.1rem solid #d9d9d9;
    }
    .dis_two,.dis_four{
        font-size: 1.5rem;
        line-height: 7rem;
    }
    .dis_three>div{
        width: 75%;
        padding: 0rem;
        display: inline-block;
    }
    .dis_three>div p{
        line-height: 2rem;
    }
    /*订单信息*/
    .dis_p p{
        padding: 0.5rem 2rem;
        font-size: 1.5rem;
        line-height: 3.5rem;
    }

</style>


