<template>
    <div class="download">
        <!--顶部-->
        <div class="detail_top"><van-nav-bar title="下载" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <div class="AllDown">
            <img :src="imgUrl" alt="">
            <p>下载饿了吗APP</p>
            <button @click="ToShow">下载</button>
        </div>
        <!--弹窗-->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show">确认</button>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "Download",
        data(){
            return{
                //弹窗显隐
                show: false,
                //弹窗内容
                text:"",
                imgUrl:"data:image/jpeg;base64,"
            }
        },
        methods: {
            onClickLeft() {
                this.$router.back(-1);
            },
            ToShow(){
                this.show=true;
                this.text="IOS用户请前往AppStore下载"
            }
        },
    }
</script>

<style scoped>
.download{
    height: 100%;
    background: white;
}
.AllDown{
    text-align: center;
    padding-top: 2.5rem;
}
.AllDown img{
    width: 50%;
    border-radius: 2rem
}
.AllDown p{
    font-size: 1.8rem;
    line-height: 3rem;
}
.AllDown button{
    width: 90%;
    border: none;
    background: #4cd964;
    font-size: 1.8rem;
    color: white;
    height: 4.5rem;
    border-radius: 0.5rem;
    margin-top: 2rem;
}
</style>


