<template>
  <div class="address">
    <div class="header">
      <div class="sousuo" @click="fanhui()">
        <van-icon name="arrow-left"/>
      </div>
      <span class="title">添加地址</span>
    </div>
    <div class="lianxi">
      <div class="lianxi_title">联系人</div>
      <div class="lianxi_input">
        <van-field v-model="name" placeholder="你的名字"/>
      </div>
      <div class="lianxi_xuanze">
        <van-radio-group v-model="radio">
          <van-radio name="1" checked-color="#07c160">先生</van-radio>
          <van-radio name="2" checked-color="#07c160">女士</van-radio>
        </van-radio-group>
      </div>
      <div class="clearfix"></div>
    </div>
    <div class="phone">
      <div class="phone_title">联系电话</div>
      <div class="phone_num">
        <van-field v-model="tel" placeholder="你的手机号"/>

      </div>
      <div class="beixuan">
        <van-field v-model="beixuan" placeholder="备选电话"/>
      </div>
      <div class="clearfix"></div>
    </div>
    <div class="songcan">
      <div class="songcan_title">送餐地址</div>
      <div class="songcan_xiaoqu" @click="getAddSite()">
        <van-field v-model="xiaoqu" placeholder="小区/写字楼/学校等"/>
      </div>
      <div class="songcan_xiangxi">
        <van-field v-model="xiangxi" placeholder="详细地址(如门牌号等)"/>
      </div>
      <div class="clearfix"></div>
    </div>
    <div class="biaoqian">
      <div class="biaoqian_title">标签</div>
      <div class="biaoqian_input">
        <van-field v-model="biaoqian" placeholder="无/家/学校/公司"/>
      </div>
      <div class="clearfix"></div>
    </div>
    <div class="button1">
      <van-button type="primary" size="large" @click="getlist()">确定</van-button>
    </div>
    <div class="mark" v-if="tishishow">
      <div class="logo el-icon-warning-outline"></div>
      <span class="tishi">{{SetTxt}}</span>
      <div class="button" @click="know()">确认</div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  export default {
    name: "Address",
    data() {
      return {
        SetTxt: '',
        // 性别
        radio: 1,
        // 名字
        name: '',
        // 电话号码
        tel: '',
        // 备选电话
        beixuan: '',
        // 小区地址
        xiaoqu: '',
        // 详细地址
        xiangxi: '',
        //标签
        biaoqian: '',
        //提示框为false
        tishishow: false,
        // 所有输入框内容
        alllist: [],
        // 订单中的状态为false
        showlist: false,
        // 用户id
        userid:0,
      }
    },
    methods: {
      getlist() {
        Vue.axios.post("https://elm.cangdu.org/v1/users/" + parseInt(this.userid) + "/addresses", {
          address: this.xiaoqu,
          address_detail: this.xiangxi,
          geohash: 34.82583,
          name: this.name,
          phone: this.tel,
          tag: this.biaoqian,
          sex: this.radio,
          phone_bk: '123123',
          tag_type: 2,
        }).then((res) => {
          console.log(res);
          this.tishishow = true;
          if (res.data.success === "添加地址成功") {
            this.$router.push({
              path: '/selectadd'
            });
          } else if (res.data.message === '地址信息错误') {
            this.SetTxt = res.data.message
          } else if (res.data.message === '详细地址信息错误') {
            this.SetTxt = res.data.message
          } else if (res.data.message === '收货人姓名错误') {
            this.SetTxt = res.data.message
          } else if (res.data.message === '标签错误') {
            this.SetTxt = res.data.message
          }
        });
        var siteArr = [];
        siteArr.push(this.radio, this.name, this.tel, this.beixuan, this.xiangxi, this.biaoqian, this.xiaoqu, this.showlist);
        this.alllist = siteArr;
        localStorage.setItem('usersite', JSON.stringify(this.alllist));
        this.$router.push({path: '/selectadd'});
      },
      fanhui() {
        this.$router.back(-1);
      },
      know() {
        this.tishishow = false;
      },
      getAddSite() {
        this.$router.push({path: '/addsite'});
      },
    },
    mounted() {
      var sitelist = JSON.parse(localStorage.site);
      console.log(sitelist)
      for (var i = 0; i < sitelist.length; i++) {
        this.xiaoqu = sitelist[i].name;
      }
      var userArr = JSON.parse(localStorage.user)
      this.userid = userArr.user_id;
    }
  }


</script>

<style scoped>
  .header {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    line-height: 4.3rem;
    background: #3190E8;
    color: white;
  }

  .sousuo {
    font-size: 2.5rem;
    display: inline-block;
  }

  .title {
    font-size: 2rem;
    margin-left: 9rem;
    font-weight: 800;
  }

  .lianxi {
    margin-top: 4.5rem;
    line-height: 9rem;
    background: white;
    border-bottom: 0.1rem solid #F5F5F5;
  }

  .lianxi_title {
    width: 20%;
    float: left;
    margin: 0 0 1.5rem 1.5rem;
  }

  .lianxi_input {
    width: 70%;
    float: right;
    border-bottom: 0.1rem solid #F5F5F5;
    height: 4.5rem;
  }

  .lianxi_xuanze {
    margin-top: 1.5rem;
    width: 70%;
    float: right;
    line-height: 2rem;
    overflow: hidden;
  }

  .van-radio:nth-child(1) {
    width: 60%;
    float: right;
  }

  .van-radio:nth-child(1) {
    width: 30%;
    float: left;
  }

  .phone {
    line-height: 9rem;
    background: white;
  }

  .phone_title {
    width: 20%;
    float: left;
    margin: 0 0 1.5rem 1.5rem;
  }

  .phone_num {
    width: 70%;
    float: right;
    border-bottom: 0.1rem solid #F5F5F5;
    height: 4.5rem;
  }

  .beixuan {
    width: 70%;
    float: right;
    height: 4.5rem;
  }

  .songcan {
    line-height: 9rem;
    background: white;
    border-bottom: 0.1rem solid #F5F5F5
  }

  .songcan_title {
    width: 20%;
    float: left;
    margin: 0 0 1.5rem 1.5rem;
  }

  .songcan_xiaoqu {
    width: 70%;
    float: right;
    border-bottom: 0.1rem solid #F5F5F5;
    height: 4.5rem;
  }

  .songcan_xiangxi {
    width: 70%;
    float: right;
    height: 4.5rem;
  }

  .biaoqian {
    line-height: 4.5rem;
    background: white;
  }

  .biaoqian_title {
    width: 20%;
    float: left;
    margin: 0 0 0 1.5rem;
  }

  .biaoqian_input {
    width: 70%;
    float: right;
    height: 4.5rem;
  }

  .button1 {
    width: 80%;
    margin: 1.5rem auto;
  }

  .mark {
    background: white;
    width: 80%;
    height: 16rem;
    position: fixed;
    top: 20rem;
    left: 10%;
    z-index: 2;
    border-radius: 5%;
  }

  .logo {
    color: #F8CB86;
    width: 100%;
    text-align: center;
    display: block;
    font-size: 7.5rem;
    margin: 0 auto;
  }

  .tishi {
    margin-top: 2rem;
    width: 100%;
    display: block;
    text-align: center;
  }

  .button {
    position: absolute;
    text-align: center;
    color: white;
    font-weight: 800;
    font-size: 2.4rem;
    left: 0;
    right: 0;
    background: #4CD964;
    height: 20%;
    vertical-align: middle;
    bottom: 0;
    border-radius: 0 0 10% 10%;
  }
</style>



