<template>
  <div class="header">
    <div class="left" @click="back1()">
      <van-icon name="arrow-left"/>
    </div>
    <div class="left" @click="getshopcar()" :class="{'bluetext': isShow}">商品</div>
    <div class="right" @click="getevaluate()" :class="{'bluetext': !isShow}">评价</div>
    <div class="clearfix"></div>
    <router-view></router-view>
  </div>
</template>

<script>
    export default {
        name: "shopcarHeader",
      data() {
          return{
            menuBtn: [],
            isColor:true,
            isShow:true,
          }
      },
      methods:{
        back1() {
          this.$router.back(-1);
        },
        getshopcar() {
          this.isShow=true,
          this.$router.push({path: '/shopcarHeader/shopcar'});
        },
        getevaluate(){
          this.isShow=false,
          this.$router.push({path: '/shopcarHeader/evaluate'});
        }
      }
    }
</script>

<style scoped>
  .header {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: 4.3rem;
    border-bottom: 0.1rem solid #EBEBEB;
    background: white;
  }

  .left, .right {
    font-size: 1.6rem;
    margin: 1.5rem;
  }

  .left:nth-child(1) {
    font-size: 2.3rem;
  }

  .right {
    margin-right: 7rem;
  }

  .bluetext {
    color: #3190E8;
    padding-bottom: 0.5rem;
    border-bottom: 0.25rem solid #3190E8;
  }


</style>



