<template>
  <div class="confirmOrder">
    <!--订单备注-->
    <div class="detail_top"><van-nav-bar title="订单备注" left-arrow @click-left="onClickLeft()" fixed="true"></van-nav-bar></div>
    <!--快速备注-->
    <div class="quick">
      <p>快速备注</p>
      <ul class="beizhu">
        <li>
          <span class="la" v-for="(pro,index) in allLa" :class="active==index?'changeColor':'oldColor'" @click="chang(index)">{{pro}}</span>
        </li>
        <li><span class="one" :class="{changeColor:isShowOne}" @click="isShowOne=!isShowOne">不要香菜</span></li>
        <li><span class="one" :class="{changeColor:isShowTwo}" @click="isShowTwo=!isShowTwo">不要洋葱</span></li>
        <li><span class="one" :class="{changeColor:isShowThree}" @click="isShowThree=!isShowThree">多点醋</span></li>
        <li><span class="one" :class="{changeColor:isShowFour}" @click="isShowFour=!isShowFour">多点葱</span></li>
        <li>
          <span class="bing" v-for="(p,i) in bingAll" :class="act==i?'changeColor':'oldColor'" @click="cha(i)">{{p}}</span>
        </li>
      </ul>
    </div>
    <!--其他备注-->
    <div class="quick_two">
      <p>其他备注</p>
      <textarea placeholder="请输入备注内容(可不填)"></textarea>
    </div>
    <div class="yesw">
      <button class="yes">确定</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: "OrderComment",
    data(){
      return{
        allLa:["不要辣","少点辣","多点辣"],
        bingAll:["去冰","少冰"],
        //点击变蓝下标
        active:5,
        act:3,
        //单选点击变蓝
        isShowOne:false,
        isShowTwo:false,
        isShowThree:false,
        isShowFour:false,
      }
    },
    methods:{
      chang(index){
        this.active=index;
      },
      cha(i){
        this.act=i;
      },
      onClickLeft() {
        this.$router.back(-1);
      }
    }
  }
</script>

<style scoped>
  /*快速备注*/
  .quick,.quick_two{
    width: 100%;
    background: white;
    margin-top: 5rem;
    padding: 2rem;
  }
  /*其他备注*/
  .quick_two{
    margin-top: 1.5rem;
  }
  .quick p,.quick_two p{
    font-size: 1.5rem;
  }
  /*其他备注框*/
  .quick_two textarea{
    width: 100%;
    height: 10rem;
    border: 0.1rem solid #efefef;
    background: #f9f9f9;
    border-radius: 0.8rem;
    margin-top: 1.5rem;
    font-size: 1.4rem;
    padding: 1rem 1.5rem;
    resize: none;
  }
  /*快速备注细节*/
  .beizhu{
    font-size: 0rem;
    margin-top: 0.5rem;
  }
  .beizhu li{
    display: inline-block;
    margin-top: 1rem;
    margin-right: 1rem;
  }
  .beizhu li span{
    display: inline-block;
    border: 0.1rem solid #469bea;
    font-size: 1.5rem;
    line-height: 3rem;
    padding: 0rem 1rem;
  }
  .one{
    border-radius: 0.5rem;
  }
  .la:first-child,.bing:first-child{
    border-radius: 0.5rem 0rem 0rem 0.5rem;
  }
  .la:last-child,.bing:last-child{
    border-radius: 0rem 0.5rem 0.5rem 0rem;
  }
  /*确定*/
  .yesw{
    width: 100%;
    padding: 2rem;
  }
  .yes{
    width: 100%;
    height: 4rem;
    border: none;
    font-size: 1.5rem;
    line-height: 4rem;
    background: #4cd964;
    color: white;
    border-radius: 0.5rem;
  }
  /*变色*/
  .changeColor{
    background: #3190e8;
    color: white;
  }
  .oldColor{
    background: white;
    color: black;
  }
</style>



