<template>
    <div class="密码登录">
        <!--顶部我的积分-->
        <div class="detail_top"><van-nav-bar title="密码登录" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--三个输入框-->
        <div class="three">
            <div class="three_one">
                <input type="text" placeholder="账号" v-model="account">
            </div>
            <div class="three_one">
                <input :type="checked?'text':'password'" placeholder="密码" v-model="password">
                <van-switch v-model="checked" active-color="#4cd964" size="3rem"/>
                <span class="ming_close">abc▪▪▪</span>
            </div>
            <div class="three_one">
                <input type="text" placeholder="验证码" v-model="verification">
                <img :src="imgUrl">
                <span class="imgurl_click" @click="changeImg">
                    <p>看不清</p>
                    <p class="changone">换一张</p>
                </span>
            </div>
        </div>
        <!--温馨提示-->
        <div class="warm_prompt">
            <p>温馨提示：未注册过的账号，登录时将自动注册</p>
            <p>注册过的用户可凭账号密码登录</p>
        </div>
        <!--提交按钮-->
        <div class="but">
            <button type="button" class="btn btn-lg btn-block" @click="judge">提交</button>
        </div>

        <!--重置密码-->
        <router-link :to="{path: '/forget'}" class="chongzhi pull-right">重置密码?</router-link>
        <!--弹窗-->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show">确认</button>
            </div>
        </transition>
    </div>
</template>

<script>
    import Vue from 'vue'//引入vue

    export default {
        name: "Login",
        data(){
            return{
                //明文密文切换
                checked: false,
                //验证码
                imgUrl:"",
                //输入框信息
                account:"",
                password:"",
                verification:"",
                //弹窗显隐
                show: false,
                //弹窗内容
                text:"",
                //返回的用户信息
                user:""
            }
        },
        methods:{
            onClickLeft() {
                this.$router.back(-1);
            },
        //    更改验证码
            changeImg(){
                Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
                    this.imgUrl=result.data.code
                }).catch((error)=>{
                    console.log(error);
                })
            },
        //    提交判断
            judge(){
                Vue.axios.post("https://elm.cangdu.org/v2/login",{username:this.account,password:this.password,captcha_code:this.verification}).then((result)=>{
                    console.log(result.data);
                    this.user=result.data.message;
                    console.log(this.user);
                    if (this.user=="用户名参数错误") {
                        this.show=true;
                        this.text="请输入手机号/邮箱/用户名"
                        this.changeImg();
                    }else if (this.user=="密码参数错误") {
                        this.show=true;
                        this.text="请输入密码"
                        this.changeImg();
                    }else if (this.user=="验证码参数错误"){
                        this.show=true;
                        this.text="请输入验证码"
                        this.changeImg();
                    } else if (this.user=="密码错误"){
                        this.show=true;
                        this.text="密码错误"
                        this.changeImg();
                    }else if (this.user=="验证码不正确") {
                        this.show=true;
                        this.text="验证码错误"
                        this.changeImg();
                    }else{
                        console.log(result.data);
                        localStorage.setItem('user',JSON.stringify(result.data));
                        this.$router.push({path:'/order1/profile'});
                        //保存到vuex中
                        this.$store.commit("ZYMpublic_mutations", result.data);
                    }
                }).catch((error)=>{
                    console.log(error);
                })
            }
        },
    //    dom树创建完成后添加图片
        mounted(){
            Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
                this.imgUrl=result.data.code
            }).catch((error)=>{
                console.log(error);
            })
        }
    }
</script>

<style scoped>
/*账号样式*/
    .three{
        width: 100%;
        height: 15rem;
        background: white;
        margin-top: 1.5rem;
    }
    /*每一横格*/
    .three_one{
        width: 100%;
        height: 5rem;
        border-bottom: 0.1rem solid #cdcdcd;
        border-top: 0.1rem solid rgba(0,0,0,0);
    }
    .three_one:nth-child(3){
        border-bottom: none;
        position: relative;
    }
    /*横格内的输入框*/
    .three_one input{
        width: 100%;
        height: 3rem;
        font-size: 1.7rem;
        padding: 0rem 1.5rem;
        margin: 1rem 0rem;
        border: none;
    }
    .three_one:nth-child(2) input{
        width: 82%;
    }
    .three_one:nth-child(3) input{
        width: 60%;
    }
    .three_one img{
        height: 3rem;
    }
    /*明密文小*/
    .van-switch{
        width: 4rem;
        height: 2rem;
        top: 0.5rem;
        background: #cdcdcd;
    }
    /*明密文按钮*/
    .van-switch div{
        width: 3rem;
        height: 3rem;
        top: -0.5rem;
        left: -1rem;
    }
    /*明密文文字*/
    .ming_close{
        font-size: 1.1rem;
        color: white;
        position: absolute;
        right: 3rem;
        top: 12.5rem;
    }
    /*看清换一张*/
    .imgurl_click{
        position: absolute;
        display: inline-block;
        width: 5rem;
        height: 4rem;
        font-size: 1.3rem;
        margin-top: 0.5rem;
        margin-left: 0.5rem;
    }
    .imgurl_click p{
        line-height: 2rem;
        display: inline-block;
        margin-bottom: 0rem;
    }
    .changone{
        color: cornflowerblue;
    }
    /*温馨提示*/
    .warm_prompt{
        width: 100%;
        line-height: 3rem;
        padding: 0rem 1.5rem;
    }
    .warm_prompt p{
        margin-bottom: 0rem;
        font-size: 1.3rem;
        color: red;
    }
    /*提交按钮*/
    .but{
        width: 100%;
        padding: 0rem 1.3rem;
    }
    .but .btn{
        background: #4cd964;
        height: 5rem;
        font-size: 2rem;
        color: white;
    }
    /*重置密码*/
    .chongzhi{
        margin: 2rem;
        text-decoration: none;
    }
</style>


