<template>
    <div class="setusername">
        <!--修改用户名-->
        <div class="detail_top"><van-nav-bar title="修改用户名" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--输入用户名-->
        <input type="text" placeholder="输入用户名" maxlength="24" v-model="userName" :class="{inputB:show}" @input="changeText">
        <p :class="{pB:show}">用户名只能修改一次（5-24字符之间）</p>
        <!--确认修改-->
        <button :class="{buttonB:userName.length<5}" @click="changeU">确认修改</button>
    </div>
</template>

<script>
    export default {
        name: "SetUserName",
        data(){
            return{
                userName:"",
                show:false
            }
        },
        methods: {
            onClickLeft() {
                this.$router.push({path:"/profile/info"});
            },
            changeText(){
                if (this.userName.length>=5){
                    this.show=false
                }
            },
            changeU(){
                // userName.length<5?show=!show:show
                if (this.userName.length<5){
                    this.show=true;
                } else {
                    this.show=false;
                    this.$store.commit("ZYMchange", this.userName);
                    this.$router.push({path:'/profile/info'});
                }
            }
        },
    }
</script>

<style scoped>
input{
    display: block;
    margin: 1rem auto;
    width: 90%;
    height: 4rem;
    background: none;
    border: 0.1rem solid #d9d9d9;
    font-size: 1.7rem;
    border-radius: 0.3rem;
}
.inputB{
     border: 0.1rem solid red;
    margin-bottom: 0.5rem;
 }
p{
    margin: 1rem auto;
    width: 90%;
    font-size: 1rem;
    color: #666;
}
.pB{
    font-size: 1.4rem;
    margin-top: 0rem;
    color: red;
}
button{
    display: block;
    margin: 0rem auto;
    width: 90%;
    height: 4rem;
    background: #3199e8;
    border: none;
    font-size: 1.5rem;
    color: white;
}
.buttonB{
    color: #add6f6;
}
</style>


