<template>
    <div class="useCart">
        <!--顶部兑换会员-->
        <div class="detail_top"><van-nav-bar title="兑换会员" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--账户-->
        <p class="purchase">成功兑换后将关联到当前账号:<span>{{users.username}}</span></p>
        <!--输入框-->
        <div class="shurukuang">
            <input type="text" placeholder="请输入10位卡号" maxlength='10' min="0" onkeyup="value=value.replace(/[^0-9]/g,'')" v-model="card_num" @input="judge">
            <input type="text" placeholder="请输入6位卡密" maxlength='6' min="0" onkeyup="value=value.replace(/[^0-9]/g,'')" v-model="card_number" @input="judge">
        </div>
        <!--兑换-->
        <button @click="conversion" :disabled="isDis" class="conversion_one">兑换</button>
        <!--温馨提示-->
        <p class="wenxts">——温馨提示——</p>
        <!--提示内容-->
        <div class="hint">
            <p>新兑换的会员服务，权益以「会员说明」为准。</p>
            <p>月卡：30次减免配送费。</p>
            <p>季卡：90次减免配送费。</p>
            <p>年卡：360次减免配送费。</p>
            <p>＊仅限蜂鸟专送订单，每日最多减免3单，每单最高减免4元（一个月按31天计算）</p>
        </div>

        <!--弹窗-->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show">确认</button>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "UseCart",
        data(){
            return{
                card_num:"",
                card_number:"",
                //兑换是否禁用
                isDis:true,
                //弹窗显隐
                show: false,
                //弹窗内容
                text:"",
            }
        },
        //函数方法
        methods: {
            onClickLeft() {
                this.$router.push({path: "/Vipcard"});
            },
            judge(){
                if (this.card_num.length==10&&this.card_number.length==6){
                    this.isDis=false;
                }else{
                    this.isDis=true;
                }
            },
            conversion(){
                this.show=true;
                this.text="无效的卡号";
            }
        },
        computed:{
            users(){
                return this.$store.state.p.ZYMpublic;
            }
        }
    }
</script>

<style scoped>
    .purchase{
        width: 100%;
        font-size: 1.5rem;
        padding: 1.5rem;
    }
    .purchase span{
        font-weight: bold;
    }
    .shurukuang input{
        width: 100%;
        height: 5rem;
        font-size: 1.5rem;
        padding-left: 1.5rem;
        background: white;
    }
    .useCart .conversion_one{
        width: 90%;
        margin-left: 5%;
        margin-top: 2rem;
        font-size: 1.5rem;
        font-weight: bold;
        line-height: 4rem;
        color: white;
        background: #4cd964;
        border: none;
        border-radius: 0.5rem;
    }
    .useCart button:disabled{
        background: #ccc;
    }
    /*温馨提示*/
    .wenxts{
        font-size: 1.5rem;
        line-height: 4rem;
        margin-top: 3rem;
        text-align: center;
        color: #999;
    }
    /*提示内容*/
    .hint{
        width: 100%;
        padding: 0rem 3.5rem;
    }
    .hint p{
        font-size: 1.1rem;
        line-height: 1.7rem;
        color: #999;
    }
</style>


