<template>
    <div class="exchange">
        <!--顶部-->
        <div class="detail_top"><van-nav-bar title="兑换红包" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <div class="input_all">
            <!--兑换码-->
            <input type="text" placeholder="请输入兑换码" class="oneInput" v-model="redeem_code" @input="judge" onkeyup="value=value.replace(/[^0-9]/g,'')">
            <!--验证码-->
            <input type="text" placeholder="验证码" class="twoInput pull-left" v-model="yzm" @input="judge" maxlength='4'>
            <!--验证码图片-->
            <div class="yzmimg pull-right">
                <img :src="imgUrl" class="pull-left">
                <span class="imgurl_click" @click="changeimg">
                    <p>看不清</p>
                    <p class="changone">换一张</p>
                </span>
            </div>
            <div class="clearfix"></div>
            <!--兑换-->
            <button class="conversion" :disabled="isDis" @click="duihuan">兑换</button>
        </div>

        <!--弹窗-->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show">确认</button>
            </div>
        </transition>
    </div>
</template>

<script>
    import Vue from "vue"
    export default {
        name: "EXchange",
        data(){
            return{
                imgUrl:"",
                //兑换禁用
                isDis:true,
                //兑换码内容
                redeem_code:"",
                //验证码输入框
                yzm:"",
                //用户信息
                allUser:{},
                //弹窗显隐
                show: false,
                //    弹窗内容
                text:"",
            }
        },
        //计算属性
        // computed(){
        //
        // },
        //事件
        methods: {
            onClickLeft() {
                this.$router.push({path:"/benefit"});
            },
            //更换验证码
            changeimg(){
                Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
                    this.imgUrl=result.data.code
                }).catch((error)=>{
                    console.log(error);
                })
            },
            //判断禁用否
            judge(){
                if (this.redeem_code.replace(/\D/g,'')&&this.yzm.length==4){
                    this.isDis=false;
                }else{
                    this.isDis=true;
                }
            },
            //兑换优惠券
            duihuan(){
                Vue.axios.post(`https://elm.cangdu.org/v1/users/${this.allUser.user_id}/hongbao/exchange`,{exchange_code:this.redeem_code,captcha_code:this.yzm}).then((result)=>{
                    if(result.data.message=="无效的兑换码"){
                        this.show=true;
                        this.text="无效的兑换码"
                    }
                }).catch((error)=>{
                    console.log(error);
                })
            }
        },
        created(){
            this.allUser=this.$store.state.p.ZYMpublic;
            Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
                this.imgUrl=result.data.code
            }).catch((error)=>{
                console.log(error);
            })
        }
    }
</script>

<style scoped>
.input_all{
    text-align: center;
    padding: 1.5rem;
}
/*输入框通用样式*/
input{
    height: 5rem;
    background: white;
    border: none;
    border-radius: 0.5rem;
    padding-left: 1.2rem;
    font-size: 1.6rem;
}
/*兑换码输入框*/
.oneInput{
    width: 100%;
    margin-bottom: 1.5rem;
}
/*验证码输入框*/
.twoInput{
    width: 57%;
}
/*验证码图片背景*/
.yzmimg{
    width: 40%;
    background: white;
    height: 5rem;
    border-radius: 0.5rem;
    padding: 0.5rem;
}
/*验证码*/
.yzmimg img{
    height: 4rem;
    width: 8rem;
}
.imgurl_click p{
    font-size: 1.2rem;
    line-height: 2rem;
}
.changone{
    color: cornflowerblue;
}
/*兑换*/
.conversion{
    width: 100%;
    height: 5rem;
    background: #4cd964;
    font-size: 1.8rem;
    line-height: 5rem;
    color: white;
    border: none;
    border-radius: 0.5rem;
    margin-top: 1.5rem;
}
    button:disabled{
        background: #cccccc;
    }
</style>


