<template>
  <div class="city">
    <!--顶部城市和返回-->
    <div class="top_zym">
      <div class="pull-left" @click="fanhui()"><span class="glyphicon glyphicon-menu-left"></span></div>
      <span class="city_name">搜索</span>
    </div>
    <!--搜索和提交-->
    <div class="search">
      <input type="text" class="form-control" placeholder="请输入小区/写字楼/学校等" v-model="address" @input="changcuo">
      <span class="glyphicon glyphicon-remove cuo" v-show="isShowNow" @click="empty"></span>
      <button @click="submit_add">搜索</button>
    </div>
    <!--搜索历史-->
    <!--<span class="history" v-show="isShow">搜索历史</span>-->
    <!--搜索出的地址-->
    <div v-for="(pro,index) in AllAddress" class="allAddress" @click="addLoc(index)">
      <router-link :to="{path:'/address'}">
        <p>{{pro.name}}</p>
        <p>{{pro.address}}</p>
      </router-link>
    </div>
    <!--未搜索出数据-->
    <p v-show="isNotSJ" class="notsj">{{noneShuj}}</p>
    <!--历史记录-->
    <div class="host">
      <div v-for="(pro,index) in LocalAdd" class="allAddress" @click="fanhuiress()">
          <p>{{pro.name}}</p>
          <p>{{pro.address}}</p>
      </div>
      <p v-show="isShowAll" @click="deleteAll">清空所有</p>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'//引入vue
  export default {
    name: "Addsite",
    data(){
      return{
        nowCity:{},
        address:"",
        AllAddress:[],
        //搜索历史显隐
        isShow:true,
        //输入框内x号
        isShowNow:false,
        //暂无数据
        noneShuj:"",
        LocalAdd:[],
        //    清除所有
        isShowAll:true,
        //    暂无数据
        isNotSJ:false,
      }
    },
    methods:{
      fanhuiress() {
        this.$router.back(-1);
      },
      fanhui(){
        this.$router.back(-1);
      },
      //清空历史记录
      deleteAll(){
        localStorage.clear();
        this.LocalAdd=[];
        if (this.noneShuj==""){
          this.isShow=false;
          this.isShowAll=false;
          this.isNotSJ=false
        }
      },
      //将历史记录存储在local中
      addLoc(index){
        var data= this.AllAddress[index];
        var result= this.LocalAdd.find(item=>{
          return item.name == data.name;
        });
        console.log(result)
        if(!result){
          this.LocalAdd.push(data);
          localStorage.setItem('site',JSON.stringify(this.LocalAdd));

        }
      },
      //如何输入框内容为空,×号隐藏
      empty(){
        this.address="";
        this.isShowNow=false;
      },
      //判断输入框内是否有信息,输入框×号的显隐
      changcuo(){
        if (this.address==""||this.address==null||this.address==undefined) {
          this.isShowNow=false;
        }else {
          this.isShowNow=true;
        }
      },
      //根据输入框内容搜索地址,如果为空触发input属性
      submit_add(){
        this.isShow=false;
        if (this.address==""||this.address==null||this.address==undefined){

        } else {
          Vue.axios.get("https://elm.cangdu.org/v1/pois",{params:{city_id:this.nowCity.id,keyword:this.address}}).then((result)=>{
            this.AllAddress=result.data;
            if(result.data.length==0){
              this.noneShuj="很抱歉,无搜索结果!"
              this.isNotSJ=true
            }
          }).catch((error)=>{
            console.log(error);
          })
        };
      }
    },
    created(){
      //判断是否为真,如果为真的话就直接赋值,如果为假则为空数组
      this.LocalAdd=JSON.parse(localStorage.getItem('site')) || [];
      if (this.LocalAdd.length>0){
        this.isShow=false;
        this.isShowAll=true;
      }else {
        this.isShow=true;
        this.isShowAll=false;
      }
      //获取home页传来的参数
      Vue.axios.get(`https://elm.cangdu.org/v1/cities/${this.$route.params.id}`).then((result)=>{
        this.nowCity=result.data;
      }).catch((error)=>{
        console.log(error);
      })
      //    城市id
      localStorage.setItem('city_id',JSON.stringify(this.$route.params.id));
    },

  }
</script>

<style scoped>
  /*顶部城市和返回*/
  .top_zym{
    width: 100%;
    height: 5rem;
    background: #449aea;
    line-height: 5rem;
    text-align: center;
    color: white;
    padding: 0px 2rem;
    position: relative;
  }
  .top_zym>a{
    color: white;
  }
  .glyphicon{
    font-size: 2rem;
    line-height: 5rem;
  }
  .changecity{
    font-size: 1.5rem;
  }
  .city_name{
    display: inline-block;
    font-weight: bolder;
    font-size: 2.3rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
  /*搜索和提交*/
  .search{
    width: 100%;
    background: white;
    margin: 1rem 0px 0px;
    text-align: center;
    padding: 1rem 0px;
    border-top: 1px solid #e4e4e4;
    border-bottom: 2px solid #e4e4e4;
    position: relative;
  }
  .search>input{
    border: 1px solid #e4e4e4;
    width: 90%;
    margin: 0px auto;
    height: 3rem;
    border-radius: 0.3rem;
    font-size: 1.5rem;
  }
  .form-control:focus {
    box-shadow: none;
  }
  .search>button{
    background: #449aea;
    color: white;
    width: 90%;
    height: 3rem;
    border: none;
    margin-top: 1rem;
    font-size: 1.3rem;
  }
  .cuo{
    position: absolute;
    right: 8%;
    font-size: 1.2rem;
    color: darkblue;
    opacity: 0.7;
  }
  /*搜索历史*/
  .history{
    display: inline-block;
    width: 100%;
    font-size: 1.3rem;
    border-bottom: 2px solid #e4e4e4;
    padding-left: 1.5rem;
  }
  /*搜索出的地址*/
  .allAddress{
    /*border: 1px solid red;*/
    background: white;
    padding: 1rem 2rem;
    border-bottom: 1px solid #999;
  }
  .allAddress>a>p:nth-child(1){
    font-size: 1.5rem;
    color: #666;
  }
  .allAddress>a>p:nth-child(2){
    font-size: 1rem;
    color: #999;
    margin-bottom: 0px;
  }
  .host>p,.notsj{
    width: 100%;
    height: 5rem;
    font-size: 2rem;
    line-height: 5rem;
    background: white;
    border-bottom: 1px solid #e4e4e4;
    text-align: center;
  }
</style>



