<template>
    <div class="profileinfo">
        <!--顶部我的-->
        <div class="detail_top"><van-nav-bar title="账户信息" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--账户信息-->
        <van-cell title="头像" is-link @click="onPickFile">
            <img src="//elm.cangdu.org/img/default.jpg" class="user_header">
        </van-cell>
        <input type="file" ref="fileInput" accept="image/*" style="display: none" @change="getFile">
        <van-cell title="用户名" is-link :to="{path:'/profile/info/setusername'}"><span>{{username}}</span></van-cell>
        <van-cell title="收货地址" is-link :to="{path:'/profile/info/address'}"></van-cell>
        <!--账号绑定-->
        <p>账号绑定</p>
        <van-cell is-link @click="goApp">
            <template slot="title">
                <i class="iconfont">&#xe68d;</i>
                <span class="custom-title">手机</span>
            </template>
        </van-cell>
        <!--安全设置-->
        <p>安全设置</p>
        <van-cell title="登录密码" is-link :to="{path:'/forget'}"><span>修改</span></van-cell>
        <!--退出登录-->
        <button type="button" class="btn btn-danger btn-block" @click="quitDl">退出登录</button>
        <!--弹窗-->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show">确认</button>
            </div>
        </transition>
        <!--退出登录确认弹窗-->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="showTwo" class="tanchuang onetc">
                <van-icon name="warning-o" />
                <br>
                <span>是否退出登录</span>
                <br>
                <button @click="showTwo=!showTwo" class="oneClick">再等等</button>
                <button @click="twoClick" class="twoClick">退出登录</button>
            </div>
        </transition>
    </div>
</template>

<script>
    import Vue from 'vue'//引入vue
    export default {
        name: "ProfileInfo",
        data(){
            return{
                show:false,
                text:"",
                //退出登录显隐
                showTwo:false,
            }
        },
        methods:{
            onClickLeft() {
                this.$router.push({path:"/order1/profile"});

            },
            //上传头像
            onPickFile () {
                this.$refs.fileInput.click()
            },
            //上传头像弹窗
            getFile () {
                this.show=true;
                this.text="上传失败";
            },
            //手机App弹窗
            goApp(){
                this.show=true;
                this.text="请在手机APP中设置";
            },
            //退出登录显隐
            quitDl(){
                this.showTwo=!this.showTwo;
            },
            //确认退出
            twoClick(){
                Vue.axios.get("https://elm.cangdu.org/admin/singout").then((result)=>{
                    console.log(result);
                    if (result.data.message="退出成功") {
                        localStorage.removeItem("user");
                        //保存到vuex中
                        this.$store.commit("ZYMpublic_mutations", {});
                        this.$router.push({path:"/order1/profile"});
                    }
                }).catch((error)=>{
                    console.log(error);
                })
            }
        },
        //计算属性
        computed:{
            username() {
                return this.$store.state.p.ZYMpublic.username;
            }
        },
    }
</script>

<style scoped>
    .user_header{
        width: 5rem;
        height: 5rem;
        border-radius: 50%;
    }
    /*前三个*/
    .van-cell{
        width: 100%;
        height: 5rem;
        font-size: 1.5rem;
        padding: 1rem 2rem;
    }
    .van-cell i{
        font-size: 1.5rem;
        color: #9d9d9d;
    }
    .van-cell i,.van-cell span{
        line-height: 3rem;
    }
    /*第一个*/
    .van-cell i{
        font-size: 1.5rem;
        color: #9d9d9d;
    }
    .van-cell:nth-child(2){
        margin-top: 1rem;
        height: 7rem;
    }
    .van-cell:nth-child(2) span,.van-cell:nth-child(2) i{
        line-height: 5rem;
    }
    /*账号绑定*/
    p{
        width: 100%;
        height: 3rem;
        line-height: 3rem;
        font-size: 1.2rem;
        padding: 0rem 2rem;
        margin-bottom: 0rem;
    }
    /*账号绑定*/
    .van-cell:nth-child(7) div i{
        font-size: 2rem;
        line-height: 3rem;
        color: #10aeff;
        vertical-align: top;
    }
    /*退出登录*/
    .btn{
        width: 95%;
        margin: 2rem auto;
        height: 4rem;
        font-size: 1.5rem;
    }
    /*退出登录弹窗*/
    .onetc{
        width: 80%;
        left: 4rem;
    }
    .onetc i{
        font-size: 10rem;
    }
    .onetc span{
        font-size: 3rem;
        margin-bottom: 4rem;
    }
    .onetc button{
        border-radius: 0.5rem;
        width: 10rem;
        height: 4rem;
        margin: 0rem 1rem 2rem;
        font-size: 1.7rem;
    }
    .oneClick{
        background: #c1c1c1!important;
    }
    .twoClick{
        background: #dd6b55;
    }
</style>


