<template>
    <div class="AddressAdd">
        <!--新增地址-->
        <div class="detail_top"><van-nav-bar title="新增地址" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--地址详情-->
        <div class="particulars">
            <input type="text" placeholder="请填写你的姓名" v-model="name" @input="redOne" :class="{inputRed:isInputOne}">
            <p v-show="isInputOne">请填写您的姓名</p>
            <input type="text" placeholder="小区/写字楼/学校等" v-model="address" @click="toAddDetail">
            <input type="text" placeholder="请填写详细送餐地址" v-model="address_detail" @input="redTwo" :class="{inputRed:isInputTwo}">
            <p v-show="isInputTwo">{{songadd}}</p>
            <input type="text" placeholder="请填写能够联系到您的手机号" v-model="phone" @input="redThree" :class="{inputRed:isInputThree}">
            <p v-show="isInputThree">{{songPhone}}</p>
            <input type="text" placeholder="备用联系电话 (选填)" v-model="phone_bk" @input="redFour" :class="{inputRed:isInputFour}">
            <p v-show="isInputFour">请输入正确的手机号</p>
        </div>
        <!--新增地址按钮-->
        <div class="success">
            <button @click="AddRess">新增地址</button>
        </div>
        <!--弹窗-->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show">确认</button>
            </div>
        </transition>
    </div>
</template>

<script>
    import Vue from 'vue'//引入vue
    export default {
        name: "AddressAdd",
        data(){
            return{
                text:"",
                show:false,
                //返回的数据
                dataMessage:"",
                //输入信息
                name:"",
                address:"",
                address_detail:"",
                phone:"",
                phone_bk:"",
                oneA:{},
                //搜索返回的信息
                oneAddress:{},
                //送餐地址
                songadd:"",
                //手机号
                songPhone:"",
                //红框是否显示
                isInputOne:false,
                isInputTwo:false,
                isInputThree:false,
                isInputFour:false,
            }
        },
        //计算属性
        computed: {
            username() {
                return this.$store.state.p.ZYMpublic
            },
            AddUser(){
                return this.$store.state.p.ZYMcity
            }
        },
        //事件
        methods: {
            onClickLeft() {
                this.$router.push({path: "/profile/info/address"});
                localStorage.removeItem("oneUserAdd");
            },
            //跳转搜索地址
            toAddDetail(){
                this.$router.push({name:"detail",params:{add:this.address}});
                localStorage.setItem('oneUserAdd',JSON.stringify({name:this.name,address_detail:this.address_detail,phone:this.phone,phone_bk:this.phone_bk}));
            },
            AddRess(){
                console.log(this.username);
                //新增地址
                Vue.axios.post(`https://elm.cangdu.org/v1/users/${this.username.user_id}/addresses`,{user_id:this.username.user_id,address:this.address,address_detail:this.address_detail,geohash:this.oneAddress.geohash,name:this.name,phone:this.phone,tag:4,sex:1,phone_bk:this.phone_bk,tag_type:4}).then((result)=>{
                    this.dataMessage=result.data.message;
                    console.log(this.dataMessage);
                    if (this.dataMessage=="地址信息错误"){
                        this.show=true;
                        this.text="地址信息错误"
                    }else if (this.dataMessage=="详细地址信息错误") {
                        this.show=true;
                        this.text="地址信息错误"
                    }else if (this.dataMessage=="收获手机号错误"){
                        this.show=true;
                        this.text="收获手机号错误"
                    }else{
                        this.show=true;
                        this.text="地址添加成功"
                        // console.log("添加地址成功");
                    }
                }).catch((error)=>{
                    console.log(error);
                })
            },
            //是否红框提示
            redOne(){
                if (this.name==""){
                    this.isInputOne=true;
                }else{
                    this.isInputOne=false;
                }
            },
            redTwo(){
                if (this.address_detail.length==0){
                    this.songadd="请详细填写送餐地址";
                    this.isInputTwo=true;
                }else if (this.address_detail.length<3){
                    this.songadd="送餐地址太短了,不能辨识";
                    this.isInputTwo=true;
                }else{
                    this.isInputTwo=false;
                }
            },
            redThree(){
                if (this.phone.length==0){
                    this.songPhone="手机号不能为空";
                    this.isInputThree=true;
                } else{
                    this.songPhone="请输入正确的手机号";
                    this.isInputThree=true;
                }

            },
            redFour(){
                if (this.phone_bk==""){
                    this.isInputFour=false;
                }else{
                    this.isInputFour=true;
                }
            }
        },
        created(){
            this.oneAddress=this.$route.params;
            console.log(this.oneAddress.geohash);
            this.address=this.oneAddress.name;
            this.oneA=JSON.parse(localStorage.getItem('oneUserAdd')) || {};
            this.name=this.oneA.name;
            this.address_detail=this.oneA.address_detail;
            this.phone=this.oneA.phone;
            this.phone_bk=this.oneA.phone_bk;
        }
    }
</script>

<style scoped>
.particulars{
    width: 100%;
    background: white;
    margin-top: 1rem;
    padding: 1rem;
}
.particulars input{
    width: 100%;
    height: 3.5rem;
    margin-bottom: 1rem;
    border: 0.1rem solid #d9d9d9;
    border-radius: 0.3rem;
    background: #f2f2f2;
    padding-left: 1rem;
    font-size: 1.4rem;
}
.success{
    width: 100%;
    padding: 1rem;
}
button{
    width: 100%;
    height: 3.5rem;
    background: #4cd964;
    border: none;
    border-radius: 0.3rem;
    font-size: 1.5rem;
    color: white;
}
/*输入框颜色*/
p{
    font-size: 1rem;
    color: red;
    margin-bottom: 1rem;
}
p:nth-child(9){
    margin-bottom: 0rem;
}
.inputRed{
    border: 0.1rem solid red!important;
}
</style>


