const productModule = {
  state: {
    active: 0,
    //订单页面传递数据
    dingdan: {},
    // 声明所有公共的数据
    ZYMpublic: JSON.parse(localStorage.getItem('user')) || {},
    ZYMcity: JSON.parse(localStorage.getItem('local')) || {},
    city_id: JSON.parse(localStorage.getItem('city_id')),
    Lbhcity: JSON.parse(localStorage.getItem('site')) || {},
    Lbhsite: JSON.parse(localStorage.getItem('usersite')) || {},
    daohang_id: JSON.parse(localStorage.getItem('a')),
    shopsID:JSON.parse(localStorage.getItem('shopsID')),

  },
  mutations: {
    ZYMpublic_mutations(state, userInfo) {
      state.ZYMpublic = userInfo;
    },
    ZYMchange(state, userNa) {
      state.ZYMpublic.username = userNa;
    },
    setActive(state, index) {
      // alert(index)
      state.active = index;
    },
    setDingDan(state, shuju) {
      state.dingdan = shuju;
    },
    setshopsid(state, id) {
      state.shopsID = id;
    },
  }
}
export default productModule;



