<template>
    <div class="commend">
        <!--顶部-->
        <div class="detail_top"><van-nav-bar title="推荐有奖" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <img src="../../static/imgs/activity.png" class="commend_img">
        <!--邀请好友-->
        <div class="van-row">
            <div class="van-col" @click="AppTan">
                <img src="data:image/png;base64,"><br>
                <span>邀请微信好友</span>
            </div>
            <div class="van-col" @click="AppTan">
                <img src="data:image/png;base64,"><br>
                <span>邀请QQ好友</span>
            </div>
            <div class="van-col" @click="AppTan">
                <img src="data:image/png;base64,"><br>
                <span>面对面邀请</span>
            </div>
        </div>
        <!--累计收益,成功邀请-->
        <div class="earnings">
            <div class="earnings_left">
                <p>累计收益</p>
                <p><span>0</span>元</p>
            </div>
            <div class="earnings_right pull-right">
                <p>成功邀请</p>
                <p><span>0</span>人</p>
            </div>
        </div>
        <!--收益明细-->
        <p class="detail">-收益明细-</p>
        <!--红包邀请提示-->
        <div class="invite">
            <img src="data:image/png;base64," alt=""><br>
            <span>还不赶紧去邀请好友</span>
        </div>
        <!--弹窗-->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show">确认</button>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "Commend",
        data(){
            return{
                //弹窗显隐
                show: false,
                //    弹窗内容
                text:"",
            }
        },
        methods: {
            onClickLeft() {
                this.$router.push({path: "/benefit"});
            },
            AppTan(){
                this.show=true;
                this.text="请在饿了吗APP中打开"
            }
        }
    }
</script>

<style scoped>
/*图片*/
.commend_img{
    width: 100%;
}
/*邀请*/
.van-row{
    width: 100%;
    height: 13rem;
    background: white;
    margin-top: 0.5rem;
    text-align: center;
    font-size: 1.2rem;
    line-height: 2rem;
}
.van-row img{
    width: 6rem;
    margin-top: 2rem;
}
.van-col{
    height: 100%;
    width: 33.3%;
}
/*累计收益,成功邀请*/
.earnings{
    width: 100%;
    margin-top: 2rem;
}
.earnings div{
    display: inline-block;
    width: 50%;
    text-align: center;
    font-size: 1.2rem;
    line-height: 2rem;
}
.earnings div:nth-child(1){
    border-right: 0.1rem solid #d9d9d9;
}
.earnings div span{
    font-size: 1.8rem;
    font-weight: bold;
}
.earnings div:nth-child(1) span{
    color: red;
}
/*收益明细*/
.detail{
    font-size: 1.3rem;
    text-align: center;
    margin-top: 3rem;
}
/*红包邀请提示*/
.invite{
    width: 100%;
    text-align: center;
    margin-top: 2.8rem;
    font-size: 1.2rem;
    line-height: 2rem;
    color: #999;
}
.invite img{
    width: 8%;
}
</style>


