<template>
    <div class="vocher">
        <div class="top">
            <router-link :to="{path:'/coupon'}">
                <i class="iconfont pull-right">&#xe661;<span>商家代金券说明</span></i>
            </router-link>
        </div>
        <!--下载或升级-->
        <div class="download">
            <img :src="imgUrl" alt="">
            <p class="djq1">无法使用代金券</p>
            <p class="djq2">非客户端或客户端版本过低</p>
            <button @click="ToDownload">下载或升级客户端</button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Vocher",
        data(){
            return{
                imgUrl:"data:image/png;base64,"
            }
        },
        methods:{
            ToDownload(){
                this.$router.push({path:"/download"});
            }
        },
    }
</script>

<style scoped>
.top{
    width: 100%;
    font-size: 1.3rem;
    padding: 2rem 2rem;
}
a{
    color: #3190e8;
}
a i{
    font-size: 1.8rem;
}
a span{
    font-size: 1.3rem;
    vertical-align: 0.2rem;
}
/*下载或升级客户端*/
.download{
    width: 100%;
    text-align: center;
    margin-top: 10rem;
}
.download img{
    width: 35%;
}
.djq1{
    font-size: 1.7rem;
    line-height: 3rem;
    margin-top: 1rem;
}
.djq2{
    font-size: 1.3rem;
    line-height: 2rem;
    color: #999;
}
.download button{
    border: none;
    width: 40%;
    height: 3.5rem;
    font-size: 1.5rem;
    background: #56d176;
    color: white;
    border-radius: 0.5rem;
    margin-top: 1rem;
}
</style>


