<template>
    <div class="redpack">
        <div class="top">
            <span>有<span class="geshu">{{allCoupon.length}}</span>个红包即将过期</span>
            <router-link :to="{path:'/benefit/hbDescription'}">
                <i class="iconfont pull-right">&#xe661;<span>红包说明</span></i>
            </router-link>
        </div>
        <!--优惠券-->
        <div class="coupon" v-for="(pro,index) in allCoupon">
            <div class="coupon_left">
                <span>¥</span><span>{{Math.floor(pro.amount)}}</span><span>.</span><span>{{(pro.amount-Math.floor(pro.amount))*10}}</span>
                <br>
                <span class="manjian">{{pro.description_map.sum_condition}}</span>
            </div>
            <div class="coupon_right pull-right">
                <div>
                    <p>{{pro.name}}</p>
                    <p>{{pro.description_map.validity_periods}}</p>
                    <p>{{pro.description_map.phone}}</p>
                </div>
                <span class="remain pull-right">{{pro.description_map.validity_delta}}</span>
            </div>
            <div class="astrict" v-show="pro.limit_map!=undefined">
                {{pro.limit_map==undefined?"":pro.limit_map.restaurant_flavor_ids}}
            </div>
        </div>
        <p class="history_pack" @click="ToHistory">查看历史红包&gt;</p>
        <!--兑换红包-->
        <div class="exchange">
            <div class="inside1" @click="EXchange">
                <p>兑换红包</p>
            </div>
            <div class="inside2 pull-right" @click="TJchange">
                <p>推荐有奖</p>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'//引入vue
    export default {
        name: "RedPack",
        data(){
            return{
                //用户所有信息
                users:{},
                //所有优惠券信息
                allCoupon:{},
            }
        },
        methods:{
            ToHistory(){
                this.$router.push({path:"/benefit/hbHistory"});
            },
            //跳转兑换红包
            EXchange(){
                this.$router.push({path:"/benefit/exchange"});
            },
            //跳转推荐页面
            TJchange(){
                this.$router.push({path:"/benefit/commend"});
            }
        },
        created(){
            this.users=this.$store.state.p.ZYMpublic;
            Vue.axios.get(`https://elm.cangdu.org/promotion/v2/users/${this.users.user_id}/hongbaos?limit=20&offset=0`).then((result)=>{
                this.allCoupon=result.data;
                console.log(this.allCoupon);
            }).catch((error)=>{
                console.log(error);
            })
        }
    }
</script>

<style scoped>
/*红包提示*/
.top{
    width: 100%;
    font-size: 1.3rem;
    padding: 2rem 2rem;
}
a{
    color: #3190e8;
}
a i{
    font-size: 1.8rem;
}
a span{
    font-size: 1.3rem;
    vertical-align: 0.2rem;
}
/*优惠券*/
.coupon{
    margin: auto;
    width: 90%;
    border-radius: 0.5rem;
    background: #fff url("../../static/imgs/bg-packet.png") repeat-x;
    background-size: 1.3rem;
    margin-bottom: 1rem;
}
/*左边优惠数*/
.coupon_left{
    width: 27%;
    height: 100%;
    padding: 3rem 0rem 1.5rem 1.5rem;
    display: inline-block;
}
.coupon_left span:nth-child(1){
    font-size: 1.3rem;
    color: red;
}
.coupon_left span:nth-child(2){
    font-size: 3.3rem;
    color: red;
}
.coupon_left span:nth-child(3){
    font-size: 2.3rem;
    color: red;
}
.coupon_left span:nth-child(4){
    font-size: 1.3rem;
    color: red;
}
.manjian{
    color: #999;
    line-height: 2rem;
    font-size: 1rem;
}
/*右边优惠时间*/
.coupon_right{
    width: 73%;
    height: 6rem;
    margin-top: 2rem;
    border-left: 0.1rem solid #d9d9d9;
}
.coupon_right div{
    width: 75%;
    display: inline-block;
    padding: 0.5rem 0rem 0rem 1rem;
}
.coupon_right div p{
    line-height: 1.5rem;
}
.coupon_right div p:nth-child(1){
    font-size: 1.5rem;
    line-height: 2rem;
}
.coupon_right div p:nth-child(2){
    font-size: 1rem;
    color: #999;
}
.coupon_right div p:nth-child(3){
    font-size: 1rem;
    color: #999;
}
.remain{
    font-size: 1.5rem;
    padding-right: 1rem;
    color: red;
}
/*要求限制*/
.astrict{
    width: 100%;
    font-size: 1.2rem;
    padding: 1.2rem 2rem;
    line-height: 2rem;
    color: #999;
    background: #f9f9f9;
}
/*历史红包*/
.history_pack{
    width: 100%;
    font-size: 1.2rem;
    color: #999;
    text-align: center;
    padding: 1rem 0rem;
    margin-bottom: 7rem;
}
/*红包个数*/
.geshu{
    color: red;
}
/*红包兑换*/
.exchange{
    width: 100%;
    background: white;
    position: fixed;
    bottom: 0rem;
}
.inside1,.inside2{
    display: inline-block;
    width: 50%;
    height: 5rem;
    font-size: 1.5rem;
    line-height: 5rem;
    text-align: center;
}
.inside1{
    border-right: 0.1rem solid #ddd;
}
</style>


