<template>
  <div class="waimai">
    <div class="header" v-for="c in city">
      <div class="sousuo" @click="getSousuo()">
        <van-icon name="search"/>
      </div>
      <span class="site">{{c.name}}</span>
      <div class="home">
        <span class="pull-right" v-show="changDL" @click="godl">登录<span class="register">注册</span></span>
        <i class="iconfont pull-right" v-show="!changDL" @click="godl">&#xe608;</i>
      </div>
    </div>
    <div class="menu">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide">
            <div class="container-fluid">
              <div class="row">
                <div class="col-xs-3" v-for="(food, index) in foodsFive" :key="index">
                  <div class="food_menu">
                    <img class="food" :src="'https://fuss10.elemecdn.com/'+food.image_url"
                         alt="">
                    <span class="food_text">{{food.title}}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="swiper-slide">
            <div class="row">
              <div class="col-xs-3" v-for="(food, index) in foodsLast" :key="index">
                <div class="food_menu">
                  <img class="food" :src="'https://fuss10.elemecdn.com/'+food.image_url"
                       alt="">
                  <span class="food_text">{{food.title}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- 如果需要分页器 -->
        <div class="swiper-pagination"></div>
      </div>
    </div>

    <div class="shops">
      <div class="name">
        <van-icon name="shop-o"/>
        <span>附近商家</span>
      </div>
      <router-link :to="{path:'/shopcarHeader'}">
        <div class="shopsmenu" v-for="(shop,index) in shops" :key="index" @click="getShopId(shop)">
          <img class="logo" :src="'https://elm.cangdu.org/img/'+shop.image_path" alt="">
          <div class="one">
            <span class="brand" v-if="shop.is_premium">品牌</span>
            <span class="shopsname">{{shop.name}}</span>
            <span class="pingjia" v-for="sup in shop.supports">{{sup.icon_name}}</span>
          </div>
          <div class="two">
          <span class="star">
            <el-rate
              v-model="shop.rating"
              disabled
              show-score
              text-color="#ff9900"
              score-template="{value}">
            </el-rate>
          </span>
            <span class="chouchu">月售{{shop.recent_order_num}}单</span>
            <span class="zuansong">{{shop.delivery_mode.text}}</span>
            <span class="zhunshi">准时达</span>
          </div>
          <div class="three">
            <span class="peisong">￥{{shop.float_minimum_order_amount}}起送/{{shop.piecewise_agent_fee.tips}}</span>
            <span class="juli">{{shop.distance}}/</span>
            <span class="shijian">{{shop.order_lead_time}}</span>
          </div>
          <div class="clearfix"></div>
        </div>
      </router-link>
    </div>
    <hr/>
  </div>
</template>

<script>
  import {Button} from 'vant';
  import Swiper from 'swiper';
  import Vue from "vue";

  Vue.use(Button);


  export default {
    name: "Waimai",
    data() {
      return {
        // 所有外卖商家数据
        foods: [],
        foodsFive: [],
        foodsLast: [],
        shops: [],
        city: [],
      }
    },
    mounted() {

      new Swiper('.swiper-container', {
        direction: 'horizontal',
        // 如果需要分页器
        pagination: {
          el: '.swiper-pagination',
        },
      });
      Vue.axios.get('https://elm.cangdu.org/v2/index_entry', {}).then((result) => {
        this.foodsFive = result.data.slice(0, 8);
        this.foodsLast = result.data.slice(8, 16);
      }).catch((error) => {
        console.log(error);
      });
      Vue.axios.get('https://elm.cangdu.org/shopping/restaurants?' + 'latitude=' + latitude1 + '&' + 'longitude=' + longitude1, {}).then((result) => {
        console.log(result.data);
        this.shops = result.data;

      }).catch((error) => {
        console.log(error);
      });
      var arrmsg = JSON.parse(localStorage.local);
      for (var i = 0; i < arrmsg.length; i++) {
        var latitude1 = arrmsg[i].latitude;
        var longitude1 = arrmsg[i].longitude
      }
      this.city = JSON.parse(localStorage.local);
    },
    methods: {
      godl() {
        this.$router.push({path: this.changeLY});
      },
      getSousuo() {
        this.$router.push({path: '/order1/search'});
      },

      getShopId(shop) {
        this.$store.commit('setshopsid',shop.id);
        localStorage.shopsfloat_delivery_fee = shop.float_delivery_fee;
        localStorage.shopscanhe = 3;
        localStorage.shopsorder_lead_time = shop.order_lead_time;
        localStorage.shopsname = shop.name;
        localStorage.shopslogo = shop.image_path;
      }
    },
    computed: {
      changeLY() {
        if (Object.keys(this.$store.state.p.ZYMpublic).length == 0) {
          return "/login";
        } else {
          return "/order1/profile"
        }
      },
      changDL() {
        if (Object.keys(this.$store.state.p.ZYMpublic).length == 0) {
          return true;
        } else {
          return false;
        }
      },
    }
  }
</script>

<style scoped>
  @import url(../../node_modules/swiper/dist/css/swiper.min.css);

  html {
    background: #F5F5F5;
  }

  .waimai {
    width: 100%;
    background: #FFFFFF;

  }

  .header {
    background: #3190E8;
    height: 4.3rem;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    color: white;
    z-index: 2;
  }

  .sousuo {
    display: inline-block;
    margin: 1.5rem 0 0 1.5rem;
    font-size: 2.5rem;
  }

  .site {
    display: inline-block;
    font-size: 2rem;
    width: 50%;
    margin-left: 1.8rem;
    height: 2rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .home {
    display: inline-block;
    float: right;
    font-size: 1.5rem;
    line-height: 4.3rem;
    margin-right: 2rem;
  }

  .menu {

  }

  .swiper-container {
    height: 24rem;
    padding-top: 5rem;
  }

  .food_menu {
    width: 6rem;
    margin: 0.5rem 0 0 0.6rem;
  }

  .food {
    display: inline-block;
    width: 5rem;
    height: 5rem;
  }

  .food_text {
    font-size: 1rem;
    margin-left: 0.7rem;
  }

  .shops {
    margin-top: 2rem;
  }

  .name {
    color: #999999;
    font-size: 1.3rem;
    margin-bottom: 2rem;
  }

  .shopsmenu {
    border-bottom: 0.1rem solid grey;
    padding: 2rem 0 1.5rem 0;
  }

  .logo {
    display: inline-block;
    width: 6.5rem;
    height: 6.5rem;
    float: left;
    margin: 0 0.7rem 0 0.5rem;
  }

  .one {
    display: inline-block;
    float: left;
  }

  .brand {
    background: #FFD930;
    font-size: 1.3rem;
    font-weight: 800;
    color: black;
  }

  .shopsname {
    display: inline-block;
    width: 7.5rem;
    font-size: 1.6rem;
    font-weight: 800;
    color: black;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-right: 7rem;
  }

  .pingjia {
    font-size: 1rem;
    margin-left: 0.2rem;
  }

  .two {
    display: inline-block;
    float: left;
    font-size: 1rem;
  }

  .star {
    font-size: 1rem;
  }

  .chouchu {
    display: inline-block;
    width: 5.5rem;
  }

  .zuansong {
    background: #2D84D4;
    color: white;
    margin-left: 9rem;
  }

  .zhunshi {
    border: 0.1rem solid #2D84D4;
    color: #2D84D4;
  }

  .three {
    display: inline-block;
    float: left;
    font-size: 1rem;
    margin-top: 0.7rem;
  }

  .peisong {
    display: inline-block;
    width: 12rem;
  }

  .juli {
    margin-left: 2rem;
  }

  .shijian {
    color: #2D84D4;
  }
</style>



