<template>
    <div class="questionDetail">
        <!--签到规则-->
        <div class="detail_top"><van-nav-bar :title=top left-arrow @click-left="onClickLeft" fixed="true"></van-nav-bar></div>
        <div class="backg" v-for="(pro,index) in allText">
            <p v-html="pro"></p>
        </div>
    </div>
</template>

<script>
    export default {
        name: "QuestionDetail",
        data(){
            return{
                allText:{}
            }
        },
        //计算属性
        computed:{
            top(){
                return this.$route.params.top;
            },
            text(){
                return this.$route.params.text;
            }
        },
        //事件
        methods:{
            onClickLeft() {
                this.$router.push({path:"/service"});
            },
        },
        created(){
              this.allText=this.text.split("###");
        },
    }
</script>

<style scoped>
.questionDetail{
    height: 100%;
    background: white;
}
.backg{
    font-size: 1.5rem;
    line-height: 2rem;
    background: white;
    padding: 1rem 2rem;
}
.backg:nth-child(2){
    padding-top: 4rem;
}
</style>


