<template>
  <div id="app">
      <Header></Header>
          <transition name="fade" mode="out-in">
            <router-view/>
          </transition>
  </div>
</template>

<script>

import Header from "./components/Header";
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'

  import "../node_modules/animate.css/animate.min.css"

export default {
  name: 'App',
  components: {Header}
}
</script>

<style>
    /*loading动画*/
    .elmloading{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0rem;
        left: 0rem;
        padding-top: 23rem;
    }
    /*图案运动*/
    .imgbox{
        width: 5.5rem;
        height: 5.5rem;
        margin: 0rem auto;
        animation: myfirst 0.7s infinite;
    }
    @keyframes myfirst
    {
        0% {margin-top:0rem;}
        50% {margin-top:5rem;}
        100% {margin-top:0rem;}
    }
    /*动画图案*/
    .imgbackground{
        width: 100%;
        height: 100%;
        background: url("../static/imgs/icon_loading.png") no-repeat;
        background-size: 100%;
        animation: myf 4.9s infinite;
    }
    @keyframes myf
    {
        0% {background-position:0% 0%;}
        14.2% {background-position:0% 0%;}
        14.2001% {background-position:0% 16%;}
        28.4% {background-position:0% 16%;}
        28.4001% {background-position:0% 33%;}
        42.6% {background-position:0% 33%;}
        42.6001% {background-position:0% 50%;}
        56.8% {background-position:0% 50%;}
        56.8001% {background-position:0% 67%;}
        71% {background-position:0% 67%;}
        71.0001% {background-position:0% 84%;}
        85.2% {background-position:0% 84%;}
        85.2001% {background-position:0% 100%;}
        100% {background-position:0% 100%;}
    }
    /*动画阴影*/
    .loadfoot{
        width: 100%;
        height: 5rem;
        text-align: center;
        position: absolute;
        top: 34rem;
    }
    .footspan{
        height: 2rem;
        width: 6rem;
        display: inline-block;
        border-radius: 50%;
        background: radial-gradient(#7E7E7E, #EDEDED);
        opacity: 0.5;
        animation: foot 0.7s infinite;
    }
    @keyframes foot
    {
        0% {width: 6rem;height: 2rem;}
        50% {width: 3rem;height: 1rem;}
        100% {width: 6rem;height: 2rem;}
    }





    @font-face {
        font-family: 'iconfont';  /* project id 1331450 */
        src: url('//at.alicdn.com/t/font_1331450_zzuyn3x5yl.eot');
        src: url('//at.alicdn.com/t/font_1331450_zzuyn3x5yl.eot?#iefix') format('embedded-opentype'),
        url('//at.alicdn.com/t/font_1331450_zzuyn3x5yl.woff2') format('woff2'),
        url('//at.alicdn.com/t/font_1331450_zzuyn3x5yl.woff') format('woff'),
        url('//at.alicdn.com/t/font_1331450_zzuyn3x5yl.ttf') format('truetype'),
        url('//at.alicdn.com/t/font_1331450_zzuyn3x5yl.svg#iconfont') format('svg');
    }
html,body,#app{
    height: 100%;
    width: 100%;
    background: #f1f1f1;
}
.iconfont{
    font-family:"iconfont" !important;
    font-size:16px;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
}

    /*vant组件样式*/
.detail_top div{
    background: #3190e8;
    color: white;
    font-size: 1.7rem;
    font-weight: bolder;
    height: 4rem;
    line-height: 4rem;
}
.detail_top i{
    color: white!important;
    font-size: 2.5rem;
    line-height: 4rem;
}
    /*弹窗动画*/
    .tan-leave-active{
        animation: bounceIn 0.5s linear reverse;
    }
    .tan-enter-active{
        animation: bounceIn 0.5s linear reverse;
    }
/*弹窗*/
  /*弹窗*/
  .tanchuang{
      position: absolute;
      width: 70%;
      border: none;
      background: white;
      text-align: center;
      top: 20rem;
      border-radius: 0.5rem;
      left: 6rem;
  }
  .tanchuang button{
      width: 100%;
      height: 4rem;
      background: #4cd964;
      border: none;
      font-size: 1.7rem;
      color: white;
      border-radius: 0rem 0rem 0.5rem 0.5rem;
  }
  .tanchuang i{
      color: #f8cb86;
      font-size: 8rem;
      margin: 1rem;
  }
  .tanchuang span{
      display: inline-block;
      font-size: 1.5rem;
      margin-bottom: 1rem;
  }
</style>



