<template>
  <div class="invoice">
    <!--顶部-->
    <div class="detail_top"><van-nav-bar title="选择发票抬头" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
    <!--是否需要发票-->
    <div class="YesOrNo">
      <span>是否需要开发票</span>
      <van-icon name="checked" class="pull-right" v-show="!showGreen" @click="showGreen=!showGreen"/>
      <van-icon name="checked" class="pull-right" color="#4cd964" v-show="showGreen"  @click="showGreen=!showGreen"/>
    </div>
    <!--确认-->
    <div class="affirm" @click="getOrder()">
      <button>确认</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: "FapiaoTitle",
    data(){
      return{
        showGreen:false,
      }
    },
    methods: {
      getOrder () {
        this.$router.push({path:'/orderconfirm'})
      },
      onClickLeft() {
        this.$router.back(-1);
      },
    }
  }
</script>

<style scoped>
  .YesOrNo{
    width: 100%;
    background: white;
    margin-top: 1rem;
    padding: 0rem 2rem;
    font-size: 2rem;
    line-height: 6rem;
  }
  .YesOrNo .van-icon{
    font-size: 2.5rem;
    line-height: 6rem;
  }
  .YesOrNo .van-icon:nth-child(2){
    color: #999999;
  }
  /*确认按钮*/
  .affirm{
    width: 100%;
    padding: 1rem 2rem;
  }
  .affirm button{
    width: 100%;
    height: 4rem;
    background: #4cd964;
    border: none;
    font-size: 1.5rem;
    line-height: 4rem;
    color: white;
    border-radius: 0.5rem;
  }
</style>



