<template>
    <div class="home">
        <!--顶部登录注册-->
       <div class="top">
           <span>ele.me</span>
           <span class="pull-right" v-show="changDL" @click="godl">登录<span class="register">注册</span></span>
           <i class="iconfont pull-right" v-show="!changDL" @click="godl">&#xe608;</i>
       </div>
        <!--定位当前城市-->
        <ul>
            <li>当前定位城市:<span class="locating pull-right">定位不准时,请在城市列表中选择</span></li>
            <router-link :to="{name:'city',params:{id:LocatingCity.id}}"><li>
                {{LocatingCity.name}}
                <span class="glyphicon glyphicon-menu-right pull-right"></span>
            </li></router-link>
        </ul>
        <!--热门城市-->
        <div class="container-fluid top_hot">
            <div class="row hot">
                <div class="col-md-12 col-sm-12 col-xs-12 hotcity">热门城市</div>
                <div class="col-md-3 col-sm-3 col-xs-3 text_cen" v-for="(pro,index) in HotCity"><router-link :to="{name:'city',params:{id:pro.id}}">{{pro.name}}</router-link></div>
            </div>
        </div>
        <!--所有城市-->
        <div class="container-fluid">
        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12 div_outer" v-for="(pro,index) in AllCity"><h4>{{index}}</h4>
                <div class="col-md-3 col-sm-3 col-xs-3 text_cen div_inside" v-for="(p,i) in pro"><router-link :to="{name:'city',params:{id:p.id}}">{{p.name}}</router-link></div>
            </div>
        </div>
        </div>
    </div>

</template>

<script>
  import Vue from 'vue'//引入vue
    export default {
        name: "Home",
        data(){
            return{
                LocatingCity:"",
                HotCity:[],
                //所有城市信息
                AllCity:[],
            }
        },
        computed:{
            changeLY(){
                if (Object.keys(this.$store.state.p.ZYMpublic).length==0){
                    return "/login";
                } else {
                    return "/order1/profile"
                }
            },
            changDL(){
                if (Object.keys(this.$store.state.p.ZYMpublic).length==0){
                    return true;
                } else {
                    return false;
                }
            }
        },
        methods:{
            sortObjByKey (obj) {
                var keys = Object.keys(obj).sort();
                var newObj = {}
                for(var i = 0; i < keys.length; i++){
                    var index = keys[i];
                    newObj[index] = obj[index];
                }
                return newObj;
            },
            godl(){
                this.$router.push({path:this.changeLY});
            }
        },
        created(){
            //定位当前城市
            Vue.axios.get("https://elm.cangdu.org/v1/cities?type=guess").then((result)=>{
                this.LocatingCity=result.data;
            }).catch((error)=>{
                console.log(error);
            });
            //获取热门城市
            Vue.axios.get("https://elm.cangdu.org/v1/cities?type=hot").then((result)=>{
                // this.LocatingCity=result.data.name;
                this.HotCity=result.data;
            }).catch((error)=>{
                console.log(error);
            });
        //    获取所有城市方法:1
            Vue.axios.get("https://elm.cangdu.org/v1/cities?type=group").then((result)=>{
                // this.AllCity=result.data;
                this.AllCity=this.sortObjByKey(result.data);
                console.log(this.AllCity);
            }).catch((error)=>{
                console.log(error);
            })
        }
    }
</script>

<style scoped>
html,body{
    width: 100%;
    height: 100%;
}
.home{
    width: 100%;
    height: 100%;
    border: 1px solid rgba(0,0,0,0);
}
/*顶部登录注册*/
    .top{
        background: #449aea;
        color: white;
        width: 100%;
        font-size: 1.5rem;
        line-height: 4rem;
        padding: 0px 1rem;
        position: fixed;
        z-index: 100;
    }
    .register{
        border-left: 1px solid white;
        padding-left: 0.5rem;
        margin-left: 0.5rem;
    }
    .top i{
        font-size: 2rem;
    }
/*定位当前城市*/
    ul{
        margin-top: 4rem;
        padding-left: 0px;
        line-height: 4rem;
        background: white;
    }
    li{
        display: inline-block;
        width: 100%;
        color: #666;
        font-size: 1.4rem;
        padding: 0px 1rem;
    }
    .locating{
        color: #9f9f9f;
        font-size: 1.2rem;
    }
    li:nth-child(1){
        border-bottom: 1px solid #e4e4e4;
    }
    li:nth-child(2){
        font-size: 2rem;
        border-bottom: 2px solid #e4e4e4;
    }
    .glyphicon{
        line-height: 4rem;
        color: #999;
    }
    a{
        display:block;
        width: 100%;
        height: 4rem;
    }
    a>li{
        color: #449aea;
    }
    /*热门城市布局*/
    .container-fluid{
        font-size: 1.6rem;
    }
    .top_hot{
        margin-top: 2rem;
    }
    .hot{
        background: white;
    }
    .hotcity{
        border-top: 2px solid #e4e4e4;
    }
    .row>div{
        border-bottom: 1px solid #e4e4e4;
        border-right: 1px solid #e4e4e4;
        line-height: 4rem;
        /*height: 4rem;*/
    }
    .text_cen{
        text-align: center;
    }
    .row>div:nth-child(4n+1){
        border-right: none;
    }
    .div_outer{
        border-top: 2px solid #e4e4e4;
        padding: 0px;
        background: white;
        margin-top: 2rem;
        color: #666;
    }
    .div_outer>h4{
        font-size: 1.6rem;
        line-height: 4rem;
        margin: 0px;
        padding-left: 2rem;
        border-bottom: 1px solid #e4e4e4;
    }
    .div_inside{
        border-bottom: 1px solid #e4e4e4;
        border-right: 1px solid #e4e4e4;
        line-height: 4rem;
    }
    .div_inside:nth-child(4n+1){
        border-right: none;
    }
    .div_inside>a{
        color: #666;
        width: 100%;
        overflow:hidden;
        white-space:nowrap;
        text-overflow:ellipsis;
    }
</style>



