<template>
    <div class="reset">
        <!--顶部我的积分-->
        <div class="detail_top"><van-nav-bar title="重置密码" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--三个输入框-->
        <div class="three">
            <div class="three_one">
                <input type="text" placeholder="账号" v-model="account">
            </div>
            <div class="three_one">
                <input type="text" placeholder="旧密码" v-model="oldPassword">
            </div>
            <div class="three_one">
                <input type="text" placeholder="请输入新密码" v-model="newPassword">
            </div>
            <div class="three_one">
                <input type="text" placeholder="请确认密码" v-model="confirmpassword">
            </div>
            <div class="three_one">
                <input type="text" placeholder="验证码" v-model="verification">
                <img :src="imgUrl">
                <span class="imgurl_click" @click="changeImg">
                    <p>看不清</p>
                    <p class="changone">换一张</p>
                </span>
            </div>
        </div>

        <!--提交按钮-->
        <div class="but">
            <button type="button" class="btn btn-lg btn-block" @click="changePass">确认修改</button>
        </div>

        <!--弹窗-->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show">确认</button>
            </div>
        </transition>
    </div>
</template>

<script>

    import Vue from 'vue'//引入vue
    import { Dialog } from 'vant';

    export default {
        name: "Reset",
        data(){
            return{
                //    验证码
                imgUrl:"",
                //    输入框信息
                account:"",
                oldPassword:"",
                newPassword:"",
                confirmpassword:"",
                verification:"",
                //弹窗显隐
                show: false,
                //    弹窗内容
                text:"",
            //    返回数据
                allData:""
            }
        },
        methods:{
            onClickLeft() {
                this.$router.back(-1);
            },
            //    更改验证码
            changeImg(){
                Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
                    this.imgUrl=result.data.code
                }).catch((error)=>{
                    console.log(error);
                })
            },
        //    修改用户信息
            changePass(){
                Vue.axios.post("https://elm.cangdu.org/v2/changepassword",{username:this.account,oldpassWord:this.oldPassword,newpassword:this.newPassword,confirmpassword:this.confirmpassword,captcha_code:this.verification}).then((result)=>{
                    console.log(result.data);
                    this.allData=result.data.message;
                    if (this.allData=="用户名参数错误") {
                        this.show=true;
                        this.text="请输入正确的账号"
                    }else if (this.allData=="必须添加旧密码"){
                        this.show=true;
                        this.text="请输入旧密码"
                    }else if (this.allData=="必须填写新密码") {
                        this.show=true;
                        this.text="请输入新密码"
                    }else if (this.allData=="必须填写确认密码") {
                        this.show=true;
                        this.text="请输确认密码"
                    }else if (this.allData=="请填写验证码") {
                        this.show=true;
                        this.text="请输验证码"
                    }else if (this.allData=="验证码不正确") {
                        this.show=true;
                        this.text="验证码不正确"
                        this.changeImg();
                    } else if (this.allData=="两次密码不一致") {
                        this.show=true;
                        this.text="两次输入的密码不一致"
                    }else if (this.allData=="密码不正确") {
                        this.show=true;
                        this.text="密码不正确"
                    }else if (this.allData=="未找到当前用户") {
                        this.show=true;
                        this.text="未找到当前用户"
                    }else if(result.data.success=="密码修改成功"){
                        this.show=true;
                        this.text="密码修改成功"
                    }
                }).catch((error)=>{
                    console.log(error);
                })
            }
        },
        //    dom树创建完成后添加图片
        mounted(){
            Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
                this.imgUrl=result.data.code
            }).catch((error)=>{
                console.log(error);
            })
        }
    }
</script>

<style scoped>
    /*账号样式*/
    .three{
        width: 100%;
        height: 25rem;
        background: white;
        margin-top: 1.5rem;
    }
    /*每一横格*/
    .three_one{
        width: 100%;
        height: 5rem;
        border-bottom: 0.1rem solid #cdcdcd;
        border-top: 0.1rem solid rgba(0,0,0,0);
    }
    .three_one:nth-child(5){
        border-bottom: none;
        position: relative;
    }
    /*横格内的输入框*/
    .three_one input{
        width: 100%;
        height: 3rem;
        font-size: 1.7rem;
        padding: 0rem 1.5rem;
        margin: 1rem 0rem;
        border: none;
    }
    .three_one:nth-child(5) input{
        width: 60%;
    }
    .three_one img{
        height: 3rem;
    }
    /*看不清换一张*/
    .imgurl_click{
        position: absolute;
        display: inline-block;
        width: 5rem;
        height: 4rem;
        font-size: 1.3rem;
        margin-top: 0.5rem;
        margin-left: 0.5rem;
    }
    .imgurl_click p{
        line-height: 2rem;
        display: inline-block;
        margin-bottom: 0rem;
    }
    .changone{
        color: cornflowerblue;
    }
    /*提交按钮*/
    .but{
        margin-top: 2.5rem;
        width: 100%;
        padding: 0rem 1.3rem;
    }
    .but .btn{
        background: #4cd964;
        height: 5rem;
        font-size: 2rem;
        color: white;
    }
</style>


