<template>
    <div class="evaluate">
        <!--各种评分-->
        <div class="grade">
            <div class="grade1">
                <p>{{serve.overall_score.toFixed(1)}}</p>
                <p>综合评价</p>
                <p>高于周边商家{{(serve.compare_rating*100).toFixed(1)}}%</p>
            </div>
            <div class="grade2">
                <p><span>服务态度</span><van-rate v-model="serve.service_score" readonly allow-half size="1rem" color="#ff9a0d" gutter="0rem"/><span class="score">{{serve.service_score.toFixed(1)}}</span></p>
                <p><span>菜品评价</span><van-rate v-model="serve.food_score" readonly allow-half size="1rem" color="#ff9a0d" gutter="0rem"/><span class="score">{{serve.food_score.toFixed(1)}}</span></p>
                <p><span>送达时间</span><span>{{serve.deliver_time}}分钟</span></p>
            </div>
        </div>
        <!--评价分类-->
        <div class="all_evaluate">
            <span class="one_rank" v-for="(pro,index) in rank" :key="index"  :class="active==index?'activeClass':'noactive'" @click="ToRank(index)">{{pro.name}}({{pro.count}})</span>
        </div>
        <!--评价详情-->
        <div class="rank_xq">
            <div class="rank_bg" v-for="(pro,index) in rankxq">
                <img src="https://fuss10.elemecdn.com/1/b5/23ca27369a0eed1ce0c3fc0a5ba8bjpeg.jpeg" class="rank_img">
                <div class="rank_right">
                    <p><span>{{pro.username}}</span><span class="pull-right">{{pro.rated_at}}</span></p>
                    <p><van-rate v-model="pro.rating_star" readonly allow-half size="1rem" color="#ff9a0d" gutter="0rem"/><span class="anshisd">{{pro.time_spent_desc}}</span></p>
                    <p>
                        <img :src="getImgPath(p.image_hash)" v-for="(p,i) in pro.item_ratings">
                    </p>
                    <p class="leibie"><span v-for="(p,i) in pro.item_ratings">{{p.food_name}}</span></p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from "vue"
    export default {
        name: "Evaluate",
        data(){
            return{
                //评价
                serve:[],
                //评价分类
                rank:[],
                active:0,
                //评价详情
                rankxq:[],
            }
        },
        methods:{
            ToRank(index){
                this.active=index;
            },
            //对分类图片做处理
            getImgPath(path){
                let suffix;
                if (!path){
                    return '//elm.cangdu.org/img/default.jpg'
                }
                if (path.indexOf('jpeg')!==-1){
                    suffix='.jpeg'
                } else{
                    suffix='.png'
                }
                let url='/'+path.substr(0,1)+'/'+path.substr(1,2)+'/'+path.substr(3)+suffix;
                return 'https://fuss10.elemecdn.com'+url
            }
        },
        created(){
            //获取商铺评分
            Vue.axios.get("https://elm.cangdu.org/ugc/v2/restaurants/1/ratings/scores").then((result)=>{
                this.serve=result.data;
                console.log(this.serve);
            }).catch((error)=>{
                console.log(error);
            })
            //获取商铺评价分类
            Vue.axios.get("https://elm.cangdu.org/ugc/v2/restaurants/1/ratings/tags").then((result)=>{
                this.rank=result.data;
                console.log(this.rank);
            }).catch((error)=>{
                console.log(error);
            });
            //获取评价信息
            Vue.axios.get("https://elm.cangdu.org/ugc/v2/restaurants/1/ratings?offset=0&limit=10").then((result)=>{
                this.rankxq=result.data
                console.log(this.rankxq);
            }).catch((error)=>{
                console.log(error);
            })
        }
    }
</script>

<style scoped>
    .van-rate{
        display: inline-block;
    }
    .grade{
        width: 100%;
        background: white;
    }
    .grade1{
        display: inline-block;
        width: 45%;
        padding: 2rem 0rem;
        text-align: center;
    }
    .grade1>p:nth-child(1){
        font-size: 3rem;
        line-height: 4rem;
        color: #ff6600;
    }
    .grade1>p:nth-child(2){
        font-size: 1.5rem;
        line-height: 2rem;
    }
    .grade1>p:nth-child(3){
        font-size: 1.1rem;
        line-height: 1.5rem;
        color: #a0a0a0;
    }
    .grade2{
        display: inline-block;
        width: 53%;
        padding: 2rem 0rem;
        text-align: left;
    }
    .grade2>p>span:nth-child(1){
        padding-right: 1.5rem;
        font-size: 1.7rem;
        line-height: 2.6rem;
    }
    .score{
        font-size: 1.3rem;
        padding-left: 0.5rem;
        color: #ff9a0d;
    }
    .grade2>p:nth-child(3)>span:nth-child(2){
        color: #a0a0a0;
        font-size: 1.2rem;
    }
    /*评价分类*/
    .all_evaluate{
        width: 100%;
        background: white;
        padding: 1.5rem;
        margin-top: 1.5rem;
    }
    .one_rank{
        display: inline-block;
        font-size: 1.3rem;
        line-height: 1.8rem;
        padding: 0.7rem 1.2rem;
        border-radius: 0.5rem;
        margin-right: 0.5rem;
        margin-top: 0.5rem;
    }
    .pink_rank{
        background: #f5f5f5!important;
        color: #adadad!important;
    }
    .activeClass{
        background: #3190e8;
        color: white;
    }
    .noactive{
        background: #ebf5ff;
        color: black;
    }
    /*评价详情*/
    .rank_xq{
        width: 100%;
        background: white;
        padding: 2rem;
    }
    .rank_bg{
        border-top: 0.1rem solid #f7f7f7;
        padding: 1rem 0rem;
    }
    .rank_img{
        width: 10%;
        border-radius: 50%;
        vertical-align: top;
    }
    .rank_right{
        width: 88%;
        display: inline-block;
        padding-left: 1rem;
    }
    .rank_right>p:nth-child(1)>span:nth-child(1){
        font-size: 1.5rem;
    }
    .rank_right>p:nth-child(1)>span:nth-child(2){
        font-size: 1rem;
        color: #a8a8a8;
    }
    .anshisd{
        font-size: 1.3rem;
    }
    .rank_right>p:nth-child(3)>img{
        width: 7rem;
        height: 7rem;
        margin: 0.5rem 0.5rem 0.5rem 0rem;
    }
    .leibie>span{
        display: inline-block;
        width: 7rem;
        height: 3rem;
        padding: 0.3rem 1rem;
        font-size: 1.5rem;
        line-height: 2rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        border-radius: 0.5rem;
        margin-right: 1rem;
        border: 1px solid #eeeeee;
        color: #9b9b9b;
    }
</style>


