<template>
  <div class="orderconfirm">
    <!--头部-->
    <div class="header">
      <div class="sousuo" @click="fanhui">
        <van-icon name="arrow-left"/>
      </div>
      <span class="title">确认订单</span>

      <div class="home">
        <span class="pull-right" v-show="changDL" @click="godl">登录<span class="register">注册</span></span>
        <i class="iconfont pull-right" v-show="!changDL" @click="godl">&#xe608;</i>
      </div>
    </div>
    <!--收货地址-->
    <div class="site" @click="getSite">
      <div class="site_txt" v-if="showsite">请添加一个收货地址</div>
        <div class="list_box" @click="getOrder()" v-if="!showsite">
          <div class="top">
            <span>{{name}}</span>
            <span>{{sex}}</span>
            <span>{{tel}}</span>
          </div>
          <div class="down">
            <span>{{biaoqian}}</span>
            <span>{{xiangxi}}</span>
          </div>
        </div>
      <div class="rigthBtn">
        <van-icon name="arrow"/>
      </div>
      <div class="clearfix"></div>
    </div>
    <!--送达时间-->
    <div class="givedata">
      <span class="data1">送达时间</span>
      <div class="dataright">
        <span class="jinkuai">尽快送达&nbsp|&nbsp预计{{shopstime}}</span>
        <span class="zhuansong">蜂鸟专送</span>
      </div>
      <div class="clearfix"></div>
    </div>
    <!--支付方式-->
    <div class="pay">
      <span class="way">支付方式</span>
      <span class="zaixian">在线支付<van-icon name="arrow"/>
      </span>
      <div class="clearfix"></div>
    </div>
    <div class="pay1">
      <span class="way" style="font-size: 1.2rem;
    color: #AAAAAA;">红包</span>
      <span class="zaixian">暂时只在饿了吗APP中支持</span>
      <div class="clearfix"></div>
    </div>
    <!--店铺商品-->
    <div class="shops">
      <div class="shopname">
        <!--:src="'http://elm.cangdu.org/img/'+val.image_path"-->
        <img class="img1" alt="" :src="'https://elm.cangdu.org/img/'+shopslogo">
        <span class="shop_name">{{shopsname}}</span>
      </div>
      <div class="food_list">
        <div class="food1" v-for="(val,index) in shopCartInfor">
          <span class="food_name">{{val.name}}</span>
          <span class="price">￥{{val.specfoods[0].price}}</span>
          <span class="shuliang">x{{val.count}}</span>
          <span class="clearfix"></span>
        </div>
        <div class="canhe">
            <span class="he">餐盒</span>
            <span class="price2">￥{{canhe}}</span>
          <span class="clearfix"></span>
        </div>
        <div class="peisong">
          <span class="pei">配送费</span>
          <span class="price3">￥{{float_delivery_fee}}</span>
          <span class="clearfix"></span>
        </div>
        <div class="clearfix"></div>
      </div>
      <div class="dingdan">
        <span class="jiage">订单￥{{allProductPrice.allFee}}</span>
        <div class="dingdanright">
          <span class="dai">待支付</span>
          <span class="price4">￥{{allProductPrice.allFee}}</span>
        </div>
      </div>
    </div>
    <!--订单备注-->
    <div class="beizhu">
      <div class="bei">
        <span class="bei_span">订单备注</span>
        <span class="kouwei" @click="getordercomment()">口味.偏好等
        <van-icon name="arrow"/></span>
        <div class="clearfix"></div>
      </div>
      <div class="fapiao">
        <span class="fapiao_txt">发票抬头</span>
        <span class="nopiao" @click="gettitle()">不需要开发票<van-icon name="arrow"/>
        </span>
        <div class="clearfix"></div>
      </div>
    </div>
    <div class="footer">
      <div class="footer_left">待支付￥{{allProductPrice.allFee}}</div>
      <div class="footer_right" @click="getzhifu()">确认下单</div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  export default {
    name: "OrderConfirm",
    data() {
      return {
        SetTxt:'',
        //购物车的订单
        shopCartInfor:[],
        // 配送费
        float_delivery_fee:0,
        //餐盒费
        canhe:0,
        // 时间
        shopstime:'',
        // 商铺名字
        shopsname:'',
        // 商铺logo
        shopslogo:'',
        // 名字
        name:'',
        // 性别
        sex:'',
        // 电话
        tel:'',
        // 标签
        biaoqian:'',
        // 地址
        dizhi:'',
        // 详细地址
        xiangxi:'',
        // 判断是否换地址信息
        showsite:true,
        //    登录/注册/用户名
        dengluzc:"登录/注册",
        // id
        userid:'',
        // 餐馆
        canguan:0,
      }
    },
    methods: {
      fanhui() {
        this.$router.back(-1);
      },
      getSite() {
        this.$router.push({path:'/selectadd'});
      },
      getordercomment() {
        this.$router.push({path: '/ordercomment'});
      },
      gettitle() {
        this.$router.push({path: '/fapiaotitle'});
      },
      addAndCount(shopCartInfor) {
        let allProInfor = shopCartInfor;
        console.log(allProInfor);

        for (var i=0; i<allProInfor.length;i++){
          var category_id = allProInfor[i].category_id;
          this.canguan = category_id;
          var attrs = allProInfor[i].attrs;
        }
        //所有数量
        let num = 0;
        //所有费用
        let allFee = 0;
        for (let val in allProInfor) {
          num += allProInfor[val].count;
          allFee += (allProInfor[val].count * allProInfor[val].specfoods[0].price);
        }
        let allfee1 = allFee + parseInt(this.float_delivery_fee)+parseInt(this.canhe)
        return {num: num, allFee: allfee1}
      },
      getzhifu(){
        // Vue.axios.post("https://elm.cangdu.org/v1/users/"+parseInt(this.userid)+"/carts/:cart_id/orders", {
        //   user_id:parseInt(this.userid),
        //   cart_id:1212,
        //   address_id:21,
        //   restaurant_id:this.canguan,
        //   geohash:'latitude=31.22967&longitude=121.4762',
        //   description:this.biaoqian,
        //   entities:[{{attrs:attrs,extra:,id:,name:,packing_fee:,price:,quantity:,sku_id:,specs:,stock:,}}]
        // }).then((res) => {
        //
        // });
        this.$router.push({path:'/confirmOrder/payment'});
      },
      godl(){
        this.$router.push({path:this.changeLY});
      }
    },
    mounted() {
      // 配送费
      this.float_delivery_fee = localStorage.shopsfloat_delivery_fee;
      // 购物车数据
      this.shopCartInfor = this.$store.state.shopsInfor.addProductCount;
      // 餐盒费
      this.canhe = localStorage.shopscanhe;
      // 商铺名字
      this.shopsname = localStorage.shopsname;
      // 商铺logo
      this.shopslogo = localStorage.shopslogo;
      var shopstime = parseInt(localStorage.shopsorder_lead_time);
      var date=new Date();
      var min=date.getMinutes();
      date.setMinutes(min+shopstime);
      var hours = date.getHours() + ':' +date.getMinutes();
      this.shopstime = hours;

    },
    created() {
        var listArr=JSON.parse(localStorage.usersite);
        console.log(listArr)
        this.name = listArr[1];
        if(listArr[0] === '男') {
          this.sex = 1;
        }else {
          this.sex = 2;
        }
        this.tel = listArr[2];
        this.biaoqian = listArr[5];
        this.xiangxi = listArr[4];
        this.showsite = listArr[7];
        this.dizhi = listArr[6];
        var userArr = JSON.parse(localStorage.user)
        this.userid = userArr.user_id;
        console.log(userArr);
    },
    computed: {
      changeLY(){
        if (Object.keys(this.$store.state.p.ZYMpublic).length==0){
          return "/login";
        } else {
          return "/order1/profile"
        }
      },
      changDL(){
        if (Object.keys(this.$store.state.p.ZYMpublic).length==0){
          return true;
        } else {
          return false;
        }
      },
      allProductPrice() {
        let shopCartInfor = this.$store.state.shopsInfor.addProductCount;
        if (shopCartInfor.length != 0) {
          this.iShow = false
        } else {
          this.iShow = true;
        }
        return this.addAndCount(shopCartInfor)
      },
    }

  }
</script>

