<template>
    <div class="selectadd">
      <div class="header">
        <div class="sousuo" @click="fanhui()">
          <van-icon name="arrow-left"/>
        </div>
        <span class="title">选择地址</span>
      </div>
      <div class="hand">

      </div>
      <div class="footer" @click="getaddsite()">
        <span class="addsite">
          <van-icon name="add-o"/>
          新增收货地址
        </span>
      </div>
      <div class="list">
        <div class="list_box" @click="getOrder()">
          <div class="top">
            <span>{{name}}</span>
            <span>{{sex}}</span>
            <span>{{tel}}</span>
          </div>
          <div class="down">
            <span>{{biaoqian}}</span>
            <span>{{xiangxi}}</span>
          </div>
        </div>
      </div>
    </div>

</template>

<script>
    export default {
        name: "selectAdd",
      data() {
          return{
            // 名字
            name:'',
            // 性别
            sex:'',
            // 电话
            tel:'',
            // 标签
            biaoqian:'',
            // 详细地址
            xiangxi:''
          }
        },
      methods:{
        fanhui() {
          this.$router.back(-1);
        },
        getaddsite() {
          this.$router.push({path:'/address'});
        },
        getOrder() {
          this.$router.push({path:'/orderconfirm'});
        }
      },
      created() {
          var listArr=JSON.parse(localStorage.usersite);
          console.log(listArr)
        this.name = listArr[1];
          this.sex = listArr[0];
          this.tel = listArr[2];
          this.biaoqian = listArr[5];
          this.xiangxi = listArr[4];
      }
    }
</script>

<style scoped>
  .header {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    line-height: 4.3rem;
    border-bottom: 0.1rem solid #EBEBEB;
    background: #3190E8;
    color: white;
  }

  .sousuo {
    font-size: 2.5rem;
    display: inline-block;
  }

  .title {
    font-size: 2rem;
    margin-left: 9rem;
    font-weight: 800;
  }
  .hand {
    background: white;
  }
  .footer {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    background: white;
    line-height: 5rem;
  }
  .addsite {
    font-size: 1.7rem;
    color: #31AEF1;
  }
  .list {
    width: 100%;
    margin-top: 5rem;
    background:white;
  }
  .list_box {
    width: 100%;
    height: 7.5rem;
    background: white;
    border-bottom: 0.1rem solid #F5F5F5;
  }
  .top {
    margin: 2rem 0 0 2rem;
  }
  .top>span:nth-child(1) {
    font-weight: 700;
    font-size: 1.8rem;
  }

  .down {
    margin: 1rem 0 0 2rem;
  }
  .down>span:nth-child(1) {
    display: inline-block;
    text-align: center;
    width: 2rem;
    background: red;
    color: white;
  }
</style>



