import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
import waimai from '../components/Waimai';
import shopcar from '../components/Shopcar';
import orderconfirm from '../components/OrderConfirm';
import address from '../components/Address';
import fapiaotitle from '../components/FapiaoTitle';
import ordercomment from '../components/OrderComment';
import selectadd from '../components/selectAdd';
import evaluate from '../components/Evaluate';
import addsite from '../components/Addsite';
import shopcarHeader from '../components/shopcarHeader';

import Home from "../components/Home";
import City from "../components/City";
import Balance from "../components/Balance";
import Points from "../components/Points"
import Detail from "../components/Detail"
import DetailJf from "../components/DetailJf"
import Discounts from "../components/Discounts"
import Profile from "../components/Profile"
import Login from "../components/Login"
import Reset from "../components/Reset"
import ProfileInfo from "../components/ProfileInfo"
import SetUserName from "../components/SetUserName"
import InfoAddress from "../components/InfoAddress"
import AddressAdd from "../components/AddressAdd"
import AddDetail from "../components/AddDetail"
import RedPack from "../components/RedPack"
import Vocher from "../components/Vocher"
import hbDescription from "../components/HBDescription"
import HBHistory from "../components/HBHistory"
import EXchange from "../components/EXchange"
import Commend from "../components/Commend"
import Download from "../components/Download"
import Coupon from "../components/Coupon"
import ChomeIndex from "../components/ChomeIndex"
import Vipcard from "../components/Vipcard"
import vipDescription from "../components/VipDescription"
import payment from "../components/Payment"
import useCart from "../components/UseCart"
import invoiceRecord from "../components/InvoiceRecord"
import service from "../components/Service"
import questionDetail from "../components/QuestionDetail"
import order1 from "../components/Order1"
import order from "../components/order"
import orderDetail from "../components/OrderDetail"
import search from "../components/Search"

export default new Router({
  routes: [
    //首页城市选择
    {path: "/", redirect: "/home"},
    {path: "/home", component: Home, name: "home"},
    //城市位置选择
    {path: "/city/:id", component: City, name: "city"},
    //底部导航
    {
      path: "/order1", component: order1, children: [
        {path: "", redirect: "waimai"},
        // 外卖页面
        {path: 'waimai', component: waimai},
        //搜索
        {path: "search", component: search},
        //订单
        {path: "order", component: order},
        //我的
        {path: "profile", component: Profile},
      ]
    },
    //我的优惠
    {
      path: "/benefit", component: Discounts, children: [
        {path: "", redirect: "redpack"},
        {path: "redpack", component: RedPack},
        {path: "vocher", component: Vocher},
      ]
    },

    {
      path:"/shopcarHeader", component: shopcarHeader, children:[
        {path: "", redirect: 'shopcar'},
        // 选取商品页面
        {path: 'shopcar', component: shopcar},
        // 评价页面
        {path: 'evaluate', component: evaluate},

      ]
    },
    //我的余额
    {path: "/balance", component: Balance},
    //我的积分
    {path: "/points", component: Points},
    //余额问题
    {path: "/balance/detail", component: Detail},
    //积分问题
    {path: "/points/detail", component: DetailJf},
    //登录
    {path: "/login", component: Login},
    //重置密码
    {path: "/forget", component: Reset},
    //账户信息
    {path: "/profile/info", component: ProfileInfo},
    //修改账号
    {path: "/profile/info/setusername", component: SetUserName},
    //编辑地址
    {path: "/profile/info/address", component: InfoAddress},
    //新增地址{
    {path: "/profile/info/address/add", component: AddressAdd, name: "add"},
    //搜索地址
    {path: "/profile/info/address/add/addDetail", component: AddDetail, name: "detail"},
    //红包说明
    {path: "/benefit/hbDescription", component: hbDescription},
    //历史红包
    {path: "/benefit/hbHistory", component: HBHistory},
    //兑换红包
    {path: "/benefit/exchange", component: EXchange},
    //推荐有奖
    {path: "/benefit/commend", component: Commend},
    //下载APP
    {path: "/download", component: Download},
    //代金券说明
    {path: "/coupon", component: Coupon},
    //积分商城
    {path: "/ChomeIndex", component: ChomeIndex},
    //会员中心
    {path: "/Vipcard", component: Vipcard},
    //会员说明
    {path: "/vipDescription", component: vipDescription},
    //在线支付
    {path: "/confirmOrder/payment", component: payment},
    //兑换会员
    {path: "/vipcard/useCart", component: useCart},
    //购买记录
    {path: "/vipcard/invoiceRecord", component: invoiceRecord},
    //服务中心
    {path: "/service", component: service},
    //签到规则
    {path: "/service/questionDetail", component: questionDetail, name: "quest"},
    //订单详情
    {path: "/order/orderDetail", component: orderDetail, name: "orderDetail"},
    // 确认订单页面
    {path: '/orderconfirm', component: orderconfirm},
    // 选择地址页面
    {path: '/selectadd', component: selectadd},
    // 添加地址页面
    {path: '/address', component: address},
    // 添加地址
    {path: '/addsite', component: addsite},
    // 订单备注页面
    {path: '/ordercomment', component: ordercomment},
    // 发条抬头页面
    {path: '/fapiaotitle', component: fapiaotitle},

  ]
});



