<template>
  <div class="order">
    <!--顶部订单-->
    <div class="detail_top">
      <van-nav-bar title="订单" left-arrow @click-left="onClickLeft" fixed="true"></van-nav-bar>
    </div>
    <!--订单信息-->
    <div class="indent" v-for="(pro,index) in allOrder" v-if="isShowAll">
      <img :src="'//elm.cangdu.org/img/'+pro.restaurant_image_url" alt="">
      <div class="indent_right pull-right">
        <div class="indent_top">
          <p>{{pro.restaurant_name}}
            <van-icon name="arrow"/>
            <span class="pull-right">{{pro.status_bar.title}}</span></p>
          <p>{{pro.formatted_created_at}}</p>
        </div>
        <p class="eat">{{pro.basket.group[0][0].name}}<span class="pull-right">￥{{Math.floor(pro.total_amount)}}.{{pro.total_amount-Math.floor(pro.total_amount)}}0</span>
        </p>
        <div class="pay">
          <button class="pull-right" @click="getShopCar(pro)">再来一单</button>
        </div>
      </div>
      <div class="clearfix"></div>
    </div>
  </div>
</template>

<script>
  import Vue from "vue";

  export default {
    name: "order",
    data() {
      return {
        allOrder: [],
        imgUrl: "//elm.cangdu.org/img/",
        isShowAll: true,
      }
    },
    //方法
    methods: {
      onClickLeft() {
        this.$router.back(-1);
      },
      ToOrderDetail(index) {
        this.$store.commit("setDingDan", this.allOrder[index]);
        this.$router.push("/order/orderDetail");
        // this.$router.push({name:"orderDetail",params:this.allOrder[index]});
      },
      getShopCar(pro) {
        this.$store.commit('setshopsid',pro.id);
        this.$router.push("/shopcarHeader/shopcar");
      }
    },
    computed: {
      users() {
        return this.$store.state.p.ZYMpublic;
      }
    },
    created() {
      Vue.axios.get(`https://elm.cangdu.org/bos/v2/users/${this.$store.state.p.ZYMpublic.user_id}/orders?limit=10&offset=0`).then((result) => {
        this.allOrder = result.data;
        console.log(this.allOrder);
      }).catch((error) => {
        console.log(error);
      });
      if (this.$route.path == "/order1/order") {
        this.$store.commit("setActive", 2);

      }
      if (Object.keys(this.$store.state.p.ZYMpublic).length == 0) {
        this.isShowAll = false;
      } else {
        this.isShowAll = true;
      }
    }
  }
</script>

<style scoped>
  /*订单列表*/
  .indent {
    width: 100%;
    background: white;
    padding: 1rem;
    border-bottom: 0.1rem solid #d9d9d9;
  }

  .indent:nth-child(2) {
    padding-top: 5rem;
  }

  /*图片*/
  .indent > img {
    width: 13%;
  }

  /*所有的商品信息*/
  .indent_right {
    width: 85%;
    display: inline-block;
  }

  /*店名和支付状态*/
  .indent_top {
    border-bottom: 0.01rem solid #f1f1f1;
    padding-bottom: 0.5rem;
  }

  /*店名*/
  .indent_top p:nth-child(1), .indent_top p:nth-child(1) i {
    font-size: 1.8rem;
    line-height: 3rem;
  }

  /*店名后的箭头*/
  .indent_top p:nth-child(1) i {
    color: #d9d9d9;
    vertical-align: -0.2rem;
  }

  /*支付状态*/
  .indent_top p:nth-child(1) span {
    font-size: 1.4rem;
  }

  .indent_top p:nth-child(2) {
    font-size: 1.3rem;
    line-height: 1.5rem;
    color: #999;
  }

  /*支付金额*/
  .eat {
    border-bottom: 0.01rem solid #f1f1f1;
    font-size: 1.5rem;
    line-height: 4rem;
  }

  /*支付额度*/
  .eat span {
    color: #ff6600;
  }

  /*按钮*/
  .pay button {
    width: 7rem;
    height: 2.3rem;
    font-size: 1.3rem;
    border-radius: 0.5rem;
    background: none;
    border: 0.1rem solid #3190e8;
    color: #3190e8;
    margin-top: 1rem;
  }
</style>



