<template>
    <div class="profile">
        <!--顶部我的-->
        <div class="detail_top"><van-nav-bar title="我的" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--登录注册-->
        <div class="register" @click="ToRegister">
            <img :src="Object.keys(username).length==0?userImg:'//elm.cangdu.org/img/default.jpg'" class="userImg">
            <div class="reg_inside">
                <span>{{Object.keys(username).length==0?dengluzc:username.username}}</span><br>
                <span class="notphone"><i class="iconfont">&#xe63d;</i>{{phone}}</span>
                <span class="glyphicon glyphicon-menu-right pull-right"></span>
            </div>
        </div>
        <!--我的余额,我的优惠,我的积分-->
        <van-row>
            <router-link :to="{path:'/balance'}">
                <van-col span="8">
                    <p>{{money}}<span>元</span></p>
                    <p>我的余额</p>
                </van-col>
            </router-link>
            <router-link :to="{path:'/benefit'}">
            <van-col span="8">
                <p>{{discounts}}<span>个</span></p>
                <p>我的优惠</p>
            </van-col>
            </router-link>
            <router-link :to="{path:'/points'}">
            <van-col span="8">
                <p>{{integral}}<span>分</span></p>
                <p>我的积分</p>
            </van-col>
            </router-link>
        </van-row>
        <!--我的订单-->
        <van-cell is-link :to="{path:'/order1/order'}">
            <template slot="title">
                <i class="iconfont">&#xe650;</i>
                <span class="custom-title">我的订单</span>
            </template>
        </van-cell>
        <van-cell is-link :to="{path:'/ChomeIndex'}">
            <template slot="title">
                <i class="iconfont red">&#xe658;</i>
                <span class="custom-title">积分商城</span>
            </template>
        </van-cell>
        <van-cell is-link :to="{path:'/Vipcard'}">
            <template slot="title">
                <i class="iconfont yellow">&#xe600;</i>
                <span class="custom-title">饿了吗会员卡</span>
            </template>
        </van-cell>
        <!--服务中心和下载-->
        <van-cell is-link :to="{path:'/service'}">
            <template slot="title">
                <i class="iconfont blue">&#xe601;</i>
                <span class="custom-title">服务中心</span>
            </template>
        </van-cell>
        <van-cell is-link :to="{path:'/download'}">
            <template slot="title">
                <i class="iconfont blue">&#xe602;</i>
                <span class="custom-title">下载饿了吗APP</span>
            </template>
        </van-cell>
    </div>
</template>

<script>
    import Vue from "vue"
    export default {
        name: "Profile",
        data(){
          return{
              money:"0.00",
              discounts:"0",
              integral:"0",
          //    登录/注册/用户名
              dengluzc:"登录/注册",
          //    绑定手机号
              phone:"暂无绑定手机号",
          //    local中的用户信息
              userAll:{},
          //    头像
              isUser:true,
          //    头像连接
          //    //elm.cangdu.org/img/default.jpg
              userImg:"../../../static/imgs/photo.png"
          }
        },
        computed:{
            username(){
                return this.$store.state.p.ZYMpublic
            },
            logout(){
                if (Object.keys(this.$store.state.p.ZYMpublic).length==0){
                    return "/login";
                } else {
                    return "/profile/info"
                }
            }
        },
        methods:{
            onClickLeft() {
                this.$router.back(-1);
            },
            ToRegister(){
                this.$router.push({path:this.logout});
            }
        },
        created(){
            if(this.$route.path=="/order1/profile"){
                this.$store.commit("setActive",3);
            }
            this.users=this.$store.state.p.ZYMpublic;
            Vue.axios.get(`https://elm.cangdu.org/promotion/v2/users/${this.users.user_id}/hongbaos?limit=20&offset=0`).then((result)=>{
                if (result.data.message=="user_id参数错误"){
                    this.discounts=0;
                }else {
                    this.discounts=result.data.length;
                }
            }).catch((error)=>{
                console.log(error);
            })
        }
    }
</script>

<style scoped>
/*登录注册*/
.register{
    width: 100%;
    height: 10rem;
    background: #3190e8;
    padding:2rem;
    border: 1px solid rgba(0,0,0,0);
}
.reg_inside{
    display: inline-block;
    color: white;
    font-size: 1.4rem;
    margin-left: 1rem;
    width: 70%;
    position: relative;
    top: -1rem;
}
.reg_inside>span:nth-child(1){
    font-weight: bolder;
    font-size: 2rem;
    line-height: 4rem;
}
.notphone>i{
    font-size: 1.7rem;
}
.glyphicon{
    position: absolute;
    right: 0px;
    line-height: 6rem;
}
/*vant栅格样式,我的三个下面间隙*/
.van-row{
    margin-bottom: 1rem;
}
/*里面文字设置*/
.van-row a div p{
    color: #4c4c4c;
    line-height: 2rem;
}
/*每一个格子的样式*/
.van-row div{
    text-align: center;
    height: 8rem;
    border: 0.1rem solid #f1f1f1;
    background: white;
    padding-top: 1.5rem;
}
.van-row div p:nth-child(1){
    font-size: 2.7rem;
    margin-bottom: 0rem;
}
div p:nth-child(1) span{
    font-size: 1.7rem;
    font-weight: normal;
    color: #666;
}
div p:nth-child(2){
    font-size: 1.4rem;
}
.van-row div:nth-child(1) p:nth-child(1){
    color: #ff9900;
    font-weight: bold;
}
.van-row div:nth-child(2) p:nth-child(1){
    color: #ff5f3e;
    font-weight: bold;
}
.van-row div:nth-child(3) p:nth-child(1){
    color: #6ac20b;
    font-weight: bold;
}
/*我的订单列表*/
/*已登录用户头像*/
.userImg{
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
}
/*我的订单向下所有*/
.van-cell div span,.van-cell i{
    font-size: 1.8rem;
    line-height: 4rem;
}
.van-cell div i{
    font-size: 2rem;
    line-height: 3rem;
}
.van-cell div{
    height: 4rem;
}
.van-cell{
    padding: 0.3rem 2rem;
}
.van-cell:nth-child(7){
    margin-top: 1rem!important;
}
.red{
    color: #fc7b53;
}
.yellow{
    color: #ffc636;
}
    .blue{
        color: #4aa5f0;
    }
</style>



