<template>
    <div class="invoiceRecord">
        <!--顶部我的-->
        <div class="detail_top"><van-nav-bar title="购买记录" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--图片-->
        <img :src="imgUrl">
        <p>没有购买记录</p>
    </div>
</template>

<script>
    export default {
        name: "InvoiceRecord",
        data(){
            return{
                imgUrl:"data:image/png;base64,"
            }
        },
        methods: {
            onClickLeft() {
                this.$router.push({path: "/Vipcard"});
            },
        },
    }
</script>

<style scoped>
    .invoiceRecord{
        height: 100%;
        background: white;
        text-align: center;
    }
    .invoiceRecord>img{
        width: 19rem;
        height: 12rem;
        margin-top: 13rem;
    }
    .invoiceRecord>p{
        margin-top: 2rem;
        font-size: 1.5rem;
        color: #999;
    }
</style>


