<template>
    <div class="points">
        <!--顶部我的积分-->
        <div class="detail_top"><van-nav-bar title="我的积分" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--金额提现-->
        <div class="bala">
            <div class="iniside_bala">
                <div class="inside_top">
                    <span>当前积分</span>
                    <router-link :to="{path:'/points/detail'}">
                        <i class="iconfont pull-right">&#xe661;<span>积分说明</span></i>
                    </router-link>
                </div>
                <p>{{integral}}<span>分</span></p>
                <button class="btn btn-default btn-lg btn-block btn-danger" @click="ToShow">积分兑换商品</button>
            </div>
        </div>
        <!--交易明细-->
        <p class="detail">最近30天积分记录</p>
        <div class="record">
            <img :src="imgUrl" alt="">
            <p>最近30天无积分记录</p>
            <span>快去下单赚取大量积分吧</span>
        </div>

        <!--弹窗-->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show">确认</button>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "balance",
        data(){
            return{
                //弹窗显隐
                show: false,
                //弹窗内容
                text:"",
                integral:"0",
                imgUrl:"data:image/png;base64,"
            }
        },
        methods:{
            onClickLeft(){
                this.$router.push("/order1/profile");
            },
            ToShow(){
                this.show=true;
                this.text="快去下单赚取大量积分吧"
            }
        }
    }
</script>

<style scoped>
    /*返回*/
    a{
        margin-left: 1rem;
        color: white;
        z-index: 500;
    }
    /*问号*/
    .iconfont{
        color: #3190e8;
        font-size: 1.7rem;
    }
    /*提现金额*/
    .bala{
        border-top: 1px solid #4a9dea;
        width: 100%;
        height: 17.5rem;
        background: #3190e8;
    }
    .iniside_bala{
        background: white;
        width: 96%;
        margin: 1rem 2%;
        border-radius: 0.3rem;
        padding: 1rem;
    }
    i>span{
        font-size: 1.3rem;
        font-weight: 200;
        vertical-align: 0.2rem;
        margin-left: 0.5rem;
    }
    .inside_top>span:nth-child(1){
        font-size: 1.3rem;
    }
    .iniside_bala>p{
        font-size: 4rem;
        margin-bottom: 0px;
    }
    .iniside_bala>p>span{
        font-size: 1.7rem;
    }
    .btn{
        margin-top: 1rem;
        font-size: 2rem;
        background: #fe6d47;
    }
    /*交易明细*/
    .detail{
        line-height: 5rem;
        font-size: 1.5rem;
        padding-left: 1rem;
        color: #999;
    }
    .record{
        text-align: center;
    }
    .record>img{
        width: 19rem;
        height: 12rem;
    }
    .record>p{
        margin-top: 2rem;
        font-size: 1.5rem;
        color: #666;
    }
    .record>span{
        font-size: 1rem;
        color: #999;
    }
</style>


