<template>
    <div class="discounts">
        <!--顶部我的优惠-->
        <div class="detail_top"><van-nav-bar title="我的优惠" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--红包和代金券-->
        <div class="red_packed" v-if="isShow">
            <div class="inside1" @click="redPacked">
                <p :class="{colorText:isColor}">红包</p>
            </div>
            <div class="inside2 pull-right" @click="vocher">
                <p :class="{colorText:!isColor}">商家代金券</p>
            </div>
        </div>
        <!--路由红包和代金券-->
        <div class="allDiscounts" v-if="isShow">
            <router-view></router-view>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Discounts",
        data(){
            return{
                isColor:true,
                isShow:true,
            }
        },
        //事件
        methods:{
            onClickLeft(){
                this.$router.push({path:"/order1/profile"});
            },
            //红包点击事件
            redPacked(){
                this.isColor=true;
                this.$router.push({path:"/benefit/redpack"});
            },
            //代金券点击事件
            vocher(){
                this.isColor=false;
                this.$router.push({path:"/benefit/vocher"});
            }
        },
        created(){
            if(this.$route.path=="/benefit/vocher"){
                this.isColor=false;
            }
            // if(){
            //
            // }
            if (Object.keys(this.$store.state.p.ZYMpublic).length==0){
                this.isShow=false;
            }else{
                this.isShow=true;
            }
        }
    }
</script>

<style scoped>
.red_packed{
    width: 100%;
    height: 5rem;
    background: white;
}
.inside1,.inside2{
    display: inline-block;
    width: 50%;
    text-align: center;
}
.red_packed p{
    display: inline-block;
    font-size: 1.5rem;
    line-height: 3rem;
    margin: 0.5rem 0rem 1.5rem;
}
.colorText{
    color: #3190e8;
    border-bottom: 0.2rem solid #3190e8;
}
</style>


