<template>
    <div class="order1">
        <router-view/>


        <!--底部导航栏-->
        <van-tabbar v-model="$store.state.p.active" fixed="true">
            <van-tabbar-item :to="{path:'/order1/waimai'}">
                <!--<nav-link>外卖</nav-link>-->
                <span>外卖</span>
                <img slot="icon" slot-scope="props" :src="props.active ? icon.active : icon.normal">
            </van-tabbar-item>
            <van-tabbar-item :to="{path:'/order1/search'}">
                <span>搜索</span>
                <img slot="icon" slot-scope="props" :src="props.active ? icon2.active2 : icon2.normal2">
            </van-tabbar-item>
            <van-tabbar-item :to="{path:'/order1/order'}">
                <span>订单</span>
                <img slot="icon" slot-scope="props" :src="props.active ? icon3.active3 : icon3.normal3">
            </van-tabbar-item>
            <van-tabbar-item :to="{path:'/order1/profile'}">
                <span>我的</span>
                <img slot="icon" slot-scope="props" :src="props.active ? icon4.active4 : icon4.normal4">
            </van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>
    export default {
        name: "Order",
        data() {
            return {
                // active: 0,
                icon: {
                    normal: '../../static/imgs/elm.png',
                    active: '../../static/imgs/elmblue.png'
                },
                icon2:{
                    normal2:"../../static/imgs/safari.png",
                    active2:"../../static/imgs/safari (1).png",
                },
                icon3:{
                    normal3:"../../static/imgs/dingdan.png",
                    active3:"../../static/imgs/dingdan1.png",
                },
                icon4:{
                    normal4:"../../static/imgs/me (2).png",
                    active4:"../../static/imgs/me.png",
                }
            }
        },
    }
</script>

<style scoped>
.van-tabbar{
    height: 6rem;
}
.van-tabbar img{
    width: 3rem;
    height: 3rem;
}
.van-tabbar span{
    font-size: 1.3rem;
}
</style>


