        <ul>
          <li v-for="(goods, index) in searchgoods" class="menu-item" :class="{'current':currentIndex === index}"
              @click="selectMenu(index, $event)" :key="index">
                <span class="text">
                  {{goods.name}}
                </span>
          </li>
        </ul>
      </div>
      <!-- E 左侧边栏 -->

      <!-- S 右侧边栏 -->
      <div class="foods-wrapper" ref="foodWrapper">
        <ul>
          <li v-for="(goods, index1) in searchgoods" class="food-list food-list-hook" :key="index1">
            <div class="menu_header">
              <span>{{goods.name}}</span>
              <span>{{goods.description}}</span>
            </div>
            <ul>
              <li v-for="(value,index) in goods.foods" class="food-item" :key="index">
                <div class="menu_box">
                  <div class="box_left">
                    <img class="logo" :src="'https://elm.cangdu.org/img/'+value.image_path" alt="">
                  </div>
                  <div class="box_right">
                    <div>
                      <h3 class="title">{{value.name}}</h3>
                    </div>
                    <div style="margin-top: 0.5rem">
                      <p class="jieshao">{{value.description}}</p>
                    </div>
                    <div style="margin-top: 0.5rem">
                      <span class="yueshou">{{value.tips}}</span>
                      <!--<span class="pinglun">好评率85%</span>-->
                    </div>
                    <div style="margin-top: 0.5rem">
                      <span class="redtxt">{{value.description}}</span>
                    </div>
                    <div class="priceall" style="margin-top: 0.5rem">
                      <span class="price1" v-for="p in value.specfoods">￥{{p.price}}</span>
                      <span class="qi">起</span>
                      <div>
                        <span class="jia" @click="addProducts(value)">+</span>
                        <span class="shuliang">{{value.count}}</span>
                        <span @click="cutProducts(value)" class="jian">-</span>
                      </div>
                    </div>
                    <div class="clearfix"></div>
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <!-- E 右侧边栏 -->

    </div>
    <!-- E 内容区域 -->
    <div class="footer">

      <div class="car" @click="writeMessageShow = !writeMessageShow">
        <van-icon name="shopping-cart"/>
      </div>
      <span class="price">￥{{allProductPrice.allFee}}</span>
      <span class="peisong">配送费￥{{float_delivery_fee}}</span>
      <span class="fukuan" v-if="iShow">还差￥20起送</span>
      <span class="fukuan greenfukuan" v-if="!iShow" @click="getOrder()">去结算</span>
      <div class="clearfix"></div>
    </div>
    <div class="carmark">
      <section class="wmassageMask" v-show="writeMessageShow" @click="writeMessageFun($event)">
        <div class="messageMaskContent" ref="msk">
          <div class="all_list">
            <div class="topmenu">
              <span class="gouwuche">购物车</span>
              <span class="delete" @click="makeShopCartEmpty()">清空</span>
              <span class="clearfix"></span>
            </div>
            <div class="list" v-for="(val,index) in shopCartProduct" :key="index" v-if="listshow">
              <span class="list_name">{{val.name}}</span>
              <div class="count">
                <span class="el-icon-circle-plus jia1" @click="addProducts(val)"></span>
                <span class="shuliang1">{{val.count}}</span>
                <span class="el-icon-remove-outline jian1" @click="cutProducts(val)"></span>
              </div>
              <span class="price1 price2">￥{{val.specfoods[0].price}}</span>
              <span class="clearfix"></span>
            </div>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import Vue from "vue";

  export default {
    name: "shopcar",
    data() {
      return {
        menuBtn: [],
        iShow: true,
        tabList: ['列表', '详情'],
        pageNum: 0,
        listHeight: [],
        scrolly: 0, // 滚动处始值
        goodList: [],
        menu: [],//全部数据
        currentIndex: 0,
        dada: [],
        listshow: true,
        writeMessageShow: false,
        cartPros: [],
        price: 0,
        //购买商品数量
        count: '1',
        // 所有商品
        searchgoods: [],
        //右侧列表滑动的y轴坐标
        scrollY: 0,
        //所有分类头部位置
        rightLiTops: [],

        //配送费
        float_delivery_fee:'',
        //结算切换
        isChang1: true,
        //减号和商品数量
        isShowCut: false,
        //是否展示购物车数量
        isShowShopCartProduct: false,
        //购物车中商品
        shopCartProduct: [],
        //是否展示购物车第一行
        isShopCart: true,
        // vuex中的shosid
        shops:0,
      }
    },
    created() {
      this.shops = this.$store.state.p.shopsID;
      // this.$nextTick 是一个异步函数，为了确保 DOM 已经渲染
      this.$nextTick(() => {
        this._initScroll()
        this._calculateHeight()
      })
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
      this.float_delivery_fee = localStorage.shopsfloat_delivery_fee;


      //获取店铺中加购的商品的数据
      this.shopCartProduct = this.$store.state.shopsInfor.addProductCount;
      // console.log(this.shopCartProduct)

      //获取商品
      Vue.axios.get("https://elm.cangdu.org/shopping/getcategory/" + this.shops).then((result) => {
        // console.log(result.data.category_list);
        this.searchgoods = result.data.category_list
      }).catch((err) => {
        console.log(err)
      });

      this.getMenu(1);
    },
    computed: {
      //动态绑定class类名
      // currentIndex (index) { //计算到达哪个区域的区间的时候的对应的索引值
      //   this.current == index;
      // for(let i = 0; i < this.listHeight.length; i++) {
      //   let height = this.listHeight[i] //当前menu子块的高度
      //   let height2 = this.listHeight[i+1] //下一个menu子块的高度
      //   //滚动到底部的时候,height2为undefined,需要考虑这种情况
      //   //需要确定是在两个menu子块的高度区间
      //   if (!height2 || (this.scrolly >= height && this.scrolly < height2)) {
      //     return i //返回这个menu子块的索引
      //   }
      // }
      // return 0
      // },

      //获取添加到购物车中的信息
      allProductPrice() {
        let shopCartInfor = this.$store.state.shopsInfor.addProductCount;
        if (shopCartInfor.length != 0) {
          this.iShow = false
        } else {
          this.iShow = true;
        }
        return this.addAndCount(shopCartInfor)
      },

    },
    methods: {
      writeMessageFun(ev) {
        if (!this.$refs.msk.contains(ev.target)) {
          this.writeMessageShow = false;
        }
      },
      getMenu(cate) {
        this.menuBtn = cate;
      },

      makeShopCartEmpty() {
        //商品为空
        this.$store.commit("emptyShopCart");

      },
      //点击展示购物车商品
      isShowShopInfor() {
        // console.log(this.shopCartProduct);
        if (this.shopCartProduct.length == 0) {
          this.isShowShopCartProduct = false
        } else {
          //显示购物车信息中购物车该列
          this.isShopCart = true;
          this.isShowShopCartProduct = !this.isShowShopCartProduct
        }
      },
      getOrder(goods) {
        this.$router.push({path: '/orderconfirm'});

      },
      //减少商品数量
      cutProducts(v) {
        if (v.count == 1) {
          v.isShow = false;
          // this.shopCartProduct.splice(v)
          //不显示蒙板
          this.isShowShopCartProduct = false;
        }
        this.$store.commit("cutProduct", v)
      },
      //添加商品数量
      addProducts(v) {
        v.isShow = true;
        //点击某个商品获取商品的信息，并添加一个count属性，记录商品的数量
        if (!v.count) {
          this.$set(v, "count", 1);
        }
        this.$store.dispatch("addProduct", v);
      },
      addAndCount(shopCartInfor) {
        let allProInfor = shopCartInfor;
        // console.log(allProInfor);
        //所有数量
        let num = 0;
        //所有费用
        let allFee = 0;
        for (let val in allProInfor) {
          num += allProInfor[val].count;
          allFee += (allProInfor[val].count * allProInfor[val].specfoods[0].price)
        }
        return {num: num, allFee: allFee}
      },
      //初始化scroll区域
      _initScroll() {
        this.menuScroll = new BScroll(this.$refs.menuWrapper, {
          click: true //结合BScroll的接口使用,是否将click事件传递,默认被拦截了
        })
        this.foodScroll = new BScroll(this.$refs.foodWrapper, {
          probeType: 3, //结合BScroll的接口使用,3实时派发scroll事件，探针的作用
          click: true
        })
        //结合BScroll的接口使用,监听scroll事件(实时派发的),并获取鼠标坐标，当滚动时能实时暴露出scroll
        this.foodScroll.on('scroll', (pos) => {
          this.scrolly = Math.abs(Math.round(pos.y))  //滚动坐标会出现负的,并且是小数,所以需要处理一下，实时取得scrollY
        })
      },
      _calculateHeight() {
        let foodList = this.$refs.foodWrapper.getElementsByClassName('food-list-hook')  //获取每一个food的dom对象
        let height = 0;
        this.listHeight.push(height)  //初始化第一个高度为0
        for (let i = 0; i < foodList.length; i++) {
          let item = foodList[i] //每一个item都是刚才获取的food的每一个dom
          height += item.clientHeight //主要是为了获取每一个foods内部块的高度
          this.listHeight.push(height)
        }
      },
      selectMenu(index, event) {
        this.currentIndex = index;
        if (!event._constructed) { //去掉自带的click事件点击，即pc端直接返回
          return;
        }
        // debugger;
        let foodList = this.$refs.foodWrapper.getElementsByClassName('food-list-hook');
        let el = foodList[index];
        //类似jump to的功能,通过这个方法,跳转到指定的dom
        this.foodScroll.scrollToElement(el, 300);
      }
    }
  }
</script>

